.card-popular {
  background-color: #f7f7f7;
  border-radius: 10px;
  padding: 40px;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: stretch;

  &__data {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    flex: 1 1 40%;
    width: 40%;
    margin-right: 20px;
  }

  &__title {
    font-size: 20px;
    line-height: 1.2;
    font-weight: 600;
    text-transform: uppercase;

    a::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }

  &__arr {
    display: block;
    width: 29px;
  }

  &__pic {
    width: 20%;
    flex: 1 1 20%;
    max-height: 125px;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }

  &:hover &__title a {
    color: var(--theme-color-accent);
  }

  &:hover &__arr {
    fill: var(--theme-color-accent);
  }

  @media (max-width: 1280px) {
    padding: 30px;

    &__title {
      font-size: 18px;
    }
  }

  @media (max-width: 1000px) {
    &__arr {
      width: 24px;
    }

    &__title {
      font-size: 16px;
    }
  }

  @media (max-width: 768px) {
    padding: 20px;

    &__arr {
      width: 20px;
      height: auto;
    }

    &__title {
      font-size: 14px;
    }
  }

  @media (max-width: 640px) {
    min-height: 120px;

    &__data {
      flex: 1 1 100%;
      width: 100%;
      margin-right: 0;
    }

    &__pic {
      position: absolute;
      right: 20px;
      top: 20px;
    }
  }

  @media (max-width: 500px) {
    padding: 15px;
    min-height: 84px;

    &__arr {
      width: 16px;
    }

    &__title {
      font-size: 12px;
    }

    &__pic {
      position: absolute;
      right: 15px;
      top: 15px;
    }
  }
}

.card-news {
  position: relative;

  &__date {
    position: absolute;
    left: 16px;
    top: 16px;
    padding: 12px 20px;
    background-color: var(--theme-color-accent);
    font-size: 14px;
    line-height: 1;
    font-weight: 600;
    color: #fff;
    border-radius: 10px;
  }

  &__photo {
    display: block;
    width: 100%;
    aspect-ratio: 493 / 280;
    border-radius: 10px;
    margin-bottom: 20px;
  }

  &__title {
    font-size: 20px;
    line-height: 1.5;
    font-weight: 700;
    margin-bottom: 12px;
    display: -webkit-box;
    line-clamp: 2;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;

    a {
      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
      }

      &:hover,
      &:focus-visible {
        color: var(--theme-color-accent);
      }
    }
  }

  &__descr {
    font-size: 14px;
    line-height: 1.5;
    font-weight: 400;
    display: -webkit-box;
    line-clamp: 2;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  @media (max-width: 768px) {
    &__date {
      left: 12px;
      top: 12px;
      padding: 8px 15px;
      font-size: 14px;
    }

    &__photo {
      margin-bottom: 17px;
    }

    &__title {
      font-size: 18px;
      margin-bottom: 9px;
    }

    &__descr {
      font-size: 14px;
    }
  }

  @media (max-width: 600px) {
    &__date {
      left: 9px;
      top: 10px;
      padding: 6px 10px;
      font-size: 12px;
    }

    &__photo {
      margin-bottom: 14px;
    }

    &__title {
      font-size: 16px;
      margin-bottom: 5px;
      margin-right: 5px;
    }

    &__descr {
      margin-right: 5px;
      font-size: 13px;
      line-clamp: 4;
      -webkit-line-clamp: 4;
    }
  }

  @media (max-width: 400px) {
    &__date {
      left: 9px;
      top: 10px;
      padding: 4px 7px;
    }

    &__photo {
      margin-bottom: 12px;
    }

    &__title {
      font-size: 14px;
      margin-bottom: 5px;
    }

    &__descr {
      font-size: 12px;
    }
  }
}

.card-info {
  background-color: var(--theme-color-accent);
  border-radius: 10px;
  color: #fff;
  min-height: 98px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 24px;

  &__label {
    font-size: 16px;
    font-weight: 600;
    line-height: 1;
    display: block;
  }

  &__pic {
    flex-shrink: 0;
    margin-right: 20px;
    background-color: #fff;
    mask-repeat: no-repeat;
    mask-size: 54px 50px;
    width: 54px;
    height: 50px;

    img {
      display: block;
      height: 100%;
      opacity: 0;
    }
  }

  @media (max-width: 640px) {
    padding: 18px;
    min-height: unset;

    &__label {
      font-size: 14px;
    }

    &__pic {
      mask-size: 35px 30px;
      margin-right: 15px;
      width: 35px;
      height: 30px;
    }
  }

  @media (max-width: 500px) {
    padding: 13px;

    &__label {
      font-size: 12px;
    }

    &__pic {
      mask-size: 27px 27px;
      margin-right: 12px;
      width: 27px;
      height: 27px;
    }
  }
}

.card-notification {
  max-width: 367px;
  position: relative;
  border-radius: 10px;
  box-shadow: 0 6px 26px 4px rgba(30, 30, 30, 0.1);
  padding: 20px;
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  min-height: 110px;
  background-color: #fff;

  @include transition(opacity var(--theme-animation-duration));

  &_removed {
    opacity: 0;
  }

  &__close {
    position: absolute;
    right: 20px;
    top: 20px;

    svg {
      fill: rgba(130, 135, 154, 0.5);
    }

    &:focus-visible,
    &:hover {
      svg {
        fill: rgba(130, 135, 154, 1);
      }
    }

    &::after {
      content: "";
      width: 30px;
      height: 30px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &__photo {
    width: 95px;
    margin-right: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;

    img {
      display: block;
      max-width: 100%;
      max-height: 70px;
    }
  }

  &__data {
    flex-grow: 1;
  }

  &__title {
    font-size: 16px;
    line-height: 1.5;
    font-weight: 600;
    color: #1e1e1e;
    margin-bottom: 6px;
  }

  &__name {
    font-size: 12px;
    line-height: 1.5;
    font-weight: 400;
    color: rgba(63, 63, 64, 0.5);
  }
}

.card-product {
  border: 1px solid #dee0e0;
  border-radius: 10px;
  background: #fff;
  display: flex;
  flex-direction: column;
  height: 100%;

  &__image {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    padding: 0px 20px 0px 20px;
    @media (max-width: 767px) {
      padding: 0px 20px 8px 20px;
    }
    img {
      max-width: 100%;
      height: 180px;
      object-fit: contain;
      @media (max-width: 767px) {
        height: 170px;
        width: 170px;
      }
    }
  }

  &__favorites {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    position: absolute;
    right: 18px;
    top: 18px;
    z-index: 5;
    background: #f7f7f7;
    border: none;
    &.active {
      border: none;
      pointer-events: none;
      background: #3f3f40;
      & svg {
        fill: #fff;
      }
    }
    @media (max-width: 767px) {
      width: 40px;
      height: 40px;
      top: 10px;
      right: 10px;
      svg {
        width: 20px;
        height: 18px;
      }
    }
  }

  &__blocks {
    position: absolute;
    top: 20px;
    left: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    z-index: 4;
    @media (max-width: 767px) {
      top: 18px;
      left: 0;
    }
  }

  &__block {
    background: #f59e0b;
    border-radius: 4px;
    padding: 6px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 100%;
    text-align: center;
    color: #fff;
    @media (max-width: 767px) {
      font-size: 12px;
      border-radius: 0px 4px 4px 0px;
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 0px 20px 20px 20px;
  }

  &__titles {
    display: flex;
    flex-direction: column;
    height: 100%;
    margin: 0px 0px 70px 0px;
    @media (max-width: 767px) {
      margin: 0px 0px 10px 0px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    margin: 0px 0px 14px 0px;
    flex: 1 1 auto;
    @media (max-width: 767px) {
      font-size: 14px;
      margin: 0px 0px 5px 0px;
    }
  }

  &__subtitle {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    display: -webkit-box;
    line-clamp: 2;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    @media (max-width: 767px) {
      font-size: 12px;
    }
  }

  &__bottom {
  }

  &__stock {
    margin: 0px 0px 20px 0px;
    font-weight: 400;
    font-size: 16px;
    line-height: 81%;
    color: #27ae60;
    @media (max-width: 767px) {
      font-size: 12px;
      margin: 0px 0px 30px 0px;
    }
  }

  &__detail {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px;
    margin: 0px 0px 24px 0px;
    @media (max-width: 767px) {
      order: 2;
      margin: 0px 0px 20px 0px;
    }
  }

  &__prices {
  }

  &__buttons {
    display: flex;
    gap: 10px 23px;
    @media (max-width: 767px) {
      gap: 10px;
      justify-content: start;
      flex-direction: row-reverse;
    }
    .btn {
      font-weight: 400;
      font-size: 18px;
      line-height: 100%;
      padding: 15px 23px;
      flex: 1 1 166px;
      border-radius: 50px;
      svg {
        margin: 0px 10px 0px 0px;
        @media (max-width: 767px) {
          width: 18px;
          height: 15px;
          margin: 0px 8px 0px 0px;
        }
      }
      @media (max-width: 992px) {
        padding: 5px 10px;
      }
      @media (max-width: 767px) {
        height: 38px;
        font-size: 14px;
      }
    }
  }
}
.prices-card-product {
  display: flex;
  flex-wrap: wrap;
  padding: 0px 0px 30px 0px;
  gap: 15px 50px;
  @media (max-width: 400px) {
    gap: 10px 15px;
    justify-content: space-between;
  }

  &__per-unit {
    span {
      font-weight: 700;
      font-size: 20px;
      line-height: 100%;
      @media (max-width: 767px) {
        font-size: 18px;
      }
    }
  }

  &__title {
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    margin: 0px 0px 10px 0px;
    color: rgba(63, 63, 64, 0.5);
    @media (max-width: 767px) {
      font-size: 12px;
      margin: 0px 0px 12px 0px;
    }
  }

  &__per-pack {
    span {
      font-weight: 600;
      font-size: 16px;
      line-height: 100%;
      @media (max-width: 767px) {
        font-size: 14px;
      }
    }
  }
}

.card-catalogs {
  background: #f7f7f7;
  border-radius: 10px;
  height: 100%;
  padding: 25px 30px 0px 30px;
  display: flex;
  flex-direction: column;
  transition: background-color 0.3s ease 0s;
  @media (max-width: 767px) {
    padding: 25px 15px 0px 15px;
  }
  &:hover {
    background-color: var(--theme-color-accent);

    .card-catalogs__title {
      color: #fff;
    }

    .btn {
      border: 1px solid #fff;
      color: #fff;

      svg {
        fill: #fff !important;
      }

      &:hover {
        color: #fff;
        opacity: 0.8;

        svg {
          fill: #fff !important;
        }
      }
    }
  }

  .btn {
    padding: 9px 20px;
    text-align: center;
    --height: 40px;
    text-transform: uppercase;
    background: transparent;
    border-radius: 34px;
    @media (max-width: 767px) {
      padding: 9px 24px;
    }

    svg {
      fill: currentColor;
      transition: fill 0.3s ease;
      margin: 0px 0px 0px 10px;
    }

    &:hover {
      background: transparent;
      opacity: 0.8;
      svg {
        fill: currentColor;
      }
    }
  }
  &__top {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    margin: 0px 0px 30px 0px;
    @media (max-width: 767px) {
      margin: 0px 0px 20px 0px;
    }
    .btn {
      border: 1px solid var(--theme-color-text-common);
      width: 100%;
      color: var(--theme-color-text-common);
      margin: 20px 0px 0px 0px;

      &:hover {
        color: var(--theme-color-text-common);

        svg {
          fill: var(--theme-color-text-common);
        }
      }
    }
  }

  &__title {
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    flex: 1 1 auto;
    min-height: 97px;
    @media (max-width: 767px) {
      font-size: 14px;
      min-height: 80px;
    }
  }

  &__bottom {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;

    img {
      margin: 0px 30px 0px 0px;
      width: 54%;
      height: 100%;
      @media (max-width: 767px) {
        margin: 0px 15px 0px 0px;
        width: 57%;
      }
    }

    .btn {
      margin: 0px 0px 30px 0px;
      &:hover {
        color: var(--theme-color-accent);

        svg {
          fill: #fff !important;
        }
      }
    }
  }
}

.cart-item {
  border: 1px solid #dee0e0;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 130px;
  @media (max-width: 767px) {
    flex-direction: column;
    justify-content: start;
    align-items: start;
    padding: 20px;
  }
  .stepcounter {
    flex: 0 0 119px;
    height: 38px;
    width: 119px;
    margin: 0px 5px 0px 0px;
    @media (max-width: 480px) {
      flex: 0 0 102px;
      width: 102px;
      margin: 0px 10px 0px 0px;
    }
  }
  .stepcounter__btn {
    width: 38px;
    height: 38px;
    flex: 0 0 38px;
  }

  &__left {
    flex: 0 1 51%;
    display: flex;
    align-items: center;
    margin: 0px 20px 0px 0px;
    @media (max-width: 767px) {
      flex: 1 1 auto;
      margin: 0px 0px 20px 0px;
    }
  }

  &__right {
    flex: 0 1 34%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px 30px 30px 0px;
    @media (max-width: 992px) {
      padding: 20px 20px 20px 0px;
    }
    @media (max-width: 767px) {
      width: 100%;
      flex: 1 1 auto;
      padding: 20px 0px 0px 0px;
    }
  }

  &__photo {
    flex: 0 0 159px;
    margin: 0px 20px 0px 0px;
    @media (max-width: 1550px) {
      margin: 0px 15px 0px 0px;
    }
    @media (max-width: 992px) {
      flex: 0 1 159px;
      margin: 0px 10px 0px 0px;
    }
    @media (max-width: 767px) {
      flex: 1 0 88px;
    }
    img {
      border-radius: 10px;
      max-width: 100%;
      max-height: 100%;
      object-fit: contain;
    }
  }

  &__titles {
    flex: 0 1 325px;
    padding: 30px 0px 30px 0px;
    @media (max-width: 767px) {
      flex: 1 1 auto;
      padding: 0px 0px 0px 0px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    &:hover {
      color: var(--theme-color-accent);
    }
    @media (max-width: 767px) {
      font-size: 14px;
    }
  }

  &__stock {
    font-weight: 400;
    font-size: 16px;
    color: #27ae60;
    margin: 2px 0px 0px 0px;
    @media (max-width: 767px) {
      font-size: 12px;
      margin: 8px 0px 0px 0px;
    }
  }

  &__summ {
    flex: 0 1 13%;
    margin: 0px 18px 0px 0px;
    padding: 30px 0px 30px 0px;
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    display: flex;
    flex-direction: column;
    @media (max-width: 1550px) {
      margin: 0px 15px 0px 0px;
    }
    @media (max-width: 992px) {
      padding: 20px 0px 20px 0px;
    }
    @media (max-width: 767px) {
      flex: 0 1 30%;
      padding: 0px 0px 0px 0px;
      margin: 0px 0px 0px 0px;
      font-size: 18px;
    }
    span {
      display: block;
      margin: 8px 0px 0px 0px;
      font-weight: 400;
      font-size: 14px;
      line-height: 100%;
      color: rgba(63, 63, 64, 0.5);
      @media (max-width: 767px) {
        font-size: 12px;
        margin: 12px 0px 0px 0px;
      }
    }
  }

  &__total {
    white-space: nowrap;
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
    margin: 0px 15px 0px 0px;
    @media (max-width: 767px) {
      font-size: 18px;
    }
  }

  &__icon {
    svg {
      fill: currentColor;
      transition: fill 0.3s ease;
    }
    &:hover {
      svg {
        fill: var(--theme-color-accent);
      }
    }
  }

  &__amount {
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;
  }
}
.btn-cart {
  font-weight: 400;
  --height: 40px;
  white-space: nowrap;
  padding: 11px 34px 14px 34px;
  font-size: 14px;
  margin: 0px 40px 0px 0px;
  svg {
    margin: 0px 10px 0px 0px;
  }
  @media (max-width: 992px) {
    padding: 5px 15px;
    margin: 0px 20px 0px 0px;
  }
  @media (max-width: 768px) {
    width: 100%;
    margin: 20px 0px 0px 0px;
  }
}
.pc-order {
  background: #f7f7f7;
  padding: 40px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px 0px;
  @media (max-width: 767px) {
    padding: 20px;
    gap: 10px 0px;
  }
  &.blue {
    svg {
      fill: var(--theme-color-accent);
    }
    span {
      color: var(--theme-color-accent);
    }
  }
  &.green {
    svg {
      fill: #27ae60;
    }
    span {
      color: #27ae60;
    }
  }
  &.red {
    svg {
      fill: #b00d0c;
    }
    span {
      color: #b00d0c;
    }
  }
  .btn {
    width: 305px;
    @media (max-width: 767px) {
      width: auto;
    }
    @media (max-width: 480px) {
      width: 100%;
    }
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 102px;
    height: 102px;
    background-color: #fff;
    border-radius: 50%;
    @media (max-width: 767px) {
      width: 60px;
      height: 60px;
    }
    svg {
      fill: var(--theme-color-accent);
      @media (max-width: 767px) {
        width: 50px;
        height: 50px;
      }
    }
  }

  &__title {
    font-weight: 400;
    font-size: 25px;
    line-height: 150%;
    text-align: center;
    display: flex;
    flex-direction: column;
    span {
      font-weight: 700;
      fill: var(--theme-color-accent);
    }
    @media (max-width: 767px) {
      font-size: 18px;
    }
  }
}

.card-catalog {
  background: #f7f7f7;
  border-radius: 10px;
  display: flex;
  min-height: 240px;
  height: 100%;
  @media (max-width: 767px) {
    width: 100%;
  }
  @media (max-width: 550px) {
    flex-direction: column;
    align-items: center;
    min-height: 100%;
  }

  &__image {
    flex: 0 0 32%;
    display: flex;
    align-items: center;
    @media (max-width: 550px) {
      flex: 1 1 auto;
      height: 180px;
    }
    img {
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }

  &__descr {
    flex: 1 1 auto;
    padding: 40px 30px 50px 10px;
    @media (max-width: 550px) {
      width: 100%;
      padding: 10px 20px 30px 20px;
    }
    ul {
      display: flex;
      flex-wrap: wrap;
      gap: 16px;
      padding: 16px 0px 0px 0px;
      @media (max-width: 550px) {
        gap: 10px 16px;
      }
      li {
        a {
          font-weight: 400;
          font-size: 14px;
          line-height: 120%;
          text-decoration: underline;
          transition: color 0.3s ease 0s;
          &:hover {
            color: var(--theme-color-accent);
          }
          @media (max-width: 767px) {
            font-size: 12px;
          }
        }
      }
    }
  }

  &__title {
    font-weight: 600;
    font-size: 16px;
    line-height: 120%;
    text-transform: uppercase;
    @media (max-width: 767px) {
      font-size: 14px;
    }
  }
}

.card-product-catalog {
  display: flex;
  border-radius: 10px;
  border: 1px solid #dee0e0;
  @media (max-width: 1550px) {
    position: relative;
    flex-direction: column;
  }
  &._spoller-active {
    .card-product-catalog__arrow {
      svg {
        transform: rotate(-180deg);
      }
    }
    .card-product-catalog__more {
      display: none;
    }
    .card-product-catalog__hide {
      display: block;
    }
  }

  &__arrow {
    display: none;
    @media (max-width: 1550px) {
      z-index: 15;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #f7f7f7;
      padding: 10px 15px;
      svg {
        display: block;
        transition: fill 0.3s ease;
        margin: 0px 0px 0px 6px;
      }
    }
    span {
      font-weight: 700;
      font-size: 10px;
      line-height: 150%;
      color: #8d8d8d;
      transition: color 0.3s ease;
    }
    &:hover {
      span {
        color: var(--theme-color-accent);
      }
      svg {
        fill: var(--theme-color-accent);
      }
    }
  }

  &__more {
  }

  &__hide {
    display: none;
  }

  &__image {
    flex: 0 0 24.1%;
    position: relative;
    border-right: 1px solid #f4f4f4;
    display: flex;
    align-items: center;
    padding: 10px 4px;
    @media (max-width: 1550px) {
      flex-direction: column;
      justify-content: center;
      flex: 1 1 auto;
      padding: 15px 15px 10px 15px;
      border: none;
    }
    .card-product__blocks {
      top: 4px;
      left: 4px;
    }
    .card-product__block {
      font-size: 12px;
      padding: 3px 7px;
      border-radius: 4px;
    }
  }

  &__photo {
    border-radius: 10px;
    width: 58px;
    height: 58px;
    object-fit: contain;
    margin: 0px 6px 0px 0px;
    @media (max-width: 1550px) {
      margin: 0px 0px 5px 0px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 12px;
    line-height: 150%;
    @media (max-width: 1550px) {
      text-align: center;
    }
  }

  &__logo {
    position: absolute;
    top: 6px;
    right: 7px;
    height: 14.5px;
    width: auto;
    @media (max-width: 1550px) {
      right: 0;
      left: 100px;
    }
  }

  &__top-block {
    position: absolute;
    top: 4px;
    height: 100%;
    width: 100%;
    left: 4px;
    display: flex;
    justify-content: space-between;
  }

  &__tables {
    @media (min-width: 1550px) {
      display: flex;
      flex: 1 1 54.1%;
    }
    @media (max-width: 1550px) {
      margin: 5px 0px 5px 0px;
    }
  }

  &__table {
    flex: 1 1 6.3%;
    padding: 10px 5px;
    border-right: 1px solid #f4f4f4;
    font-weight: 700;
    font-size: 12px;
    line-height: 150%;
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width: 1550px) {
      padding: 5px 15px 5px 15px;
      flex: 1 1 auto;
      align-items: flex-end;
      justify-content: space-between;
      border-bottom: 1px solid #f4f4f4;
      &:last-child {
        border-bottom: none;
      }
    }
    span {
      display: none;
      font-size: 10px;
      color: #8d8d8d;
      @media (max-width: 1550px) {
        display: flex;
        margin: 0px 20px 0px 0px;
      }
    }
    .table-materials {
      gap: 4px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      max-width: 80px;
      @media (max-width: 1550px) {
        justify-content: flex-end;
      }
      div {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 4px;
        border-radius: 3px;
        width: 16px;
        height: 16px;
      }
    }
    .table-type {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      gap: 7px;
      div {
        flex: 0 0 18px;
        height: 18px;
      }
    }
  }

  &__buy {
    flex: 0 0 21.8%;
    display: flex;
    align-items: center;
    padding: 7px 12px 10px 20px;
    @media (max-width: 1550px) {
      flex-direction: column;
      justify-content: center;
      flex: 1 1 auto;
      padding: 16px 15px 10px 15px;
    }

    .card-product__favorites {
      cursor: pointer;
      flex: 0 0 40px;
      --height: 40px;
      position: relative;
      width: 40px;
      height: 40px;
      top: 0;
      left: 0;
      margin: 0px 0px 0px 11px;
      svg {
        margin: 2px 0px 0px 2px;
      }
      @media (max-width: 1550px) {
        display: none;
      }
    }
    .favorites-mob {
      display: none;
      @media (max-width: 1550px) {
        display: flex;
        margin: 0px 0px 0px 16px;
      }
    }
  }

  &__buy-descr {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    @media (max-width: 1550px) {
      flex-direction: row;
      justify-content: center;
      width: 100%;
    }
    .btn-catalog {
      width: 100%;
      --height: 40px;
      font-weight: 400;
      font-size: 14px;
      line-height: 100%;
      svg {
        margin: 0px 10px 0px 0px;
      }
      &.hidden {
        display: none;
      }
      @media (max-width: 1550px) {
        width: 168px;
      }
      @media (max-width: 340px) {
        max-width: 168px;
      }
    }
    .stepcounter {
      display: none;
      width: 163px;
      height: 40px;
      border-radius: 50px;
      background-color: var(--theme-color-accent);
      flex: 1 1 auto;
      align-items: center;
      &.active {
        display: inline-flex;
      }
      @media (max-width: 1550px) {
        flex: 0 0 163px;
      }
      @media (max-width: 340px) {
        flex: 1 1 auto;
      }
    }
    .stepcounter__btn {
      border: none;
      flex: 0 0 36px;
      width: 36px;
      height: 36px;
      margin: 0px 2px;
      background: #347eca;

      &[data-stepcounter="-"] svg {
        width: 7px;
        fill: #fff;
      }
      &[data-stepcounter="+"] svg {
        width: 9px;
        height: 9px;
        fill: #fff;
      }
    }
    .stepcounter__input {
      color: #fff;
      font-weight: 400;
      font-size: 14px;
    }
    .stock {
      font-size: 12px;
      margin: 10px 0px 0px 0px;
      @media (max-width: 1550px) {
        display: none;
      }
    }
  }

  .stock-mob {
    display: none;
    font-size: 12px;
    margin: 5px 0px 0px 0px;
    @media (max-width: 1550px) {
      display: block;
    }
  }

  &__price {
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    margin: 0px 0px 6px 0px;
    @media (max-width: 1550px) {
      margin: 0px 9px 0px 0px;
    }
  }
}
