.header {
  border-bottom: 1px var(--theme-border-color) solid;
  position: relative;
  top: 0;
  background-color: #fff;
  z-index: 50;
  padding-top: 16px;
  padding-bottom: 16px;
  .filter-open & {
    z-index: 40;
  }

  &__grid {
    width: 100%;
    display: grid;
    grid-template-columns: auto auto 1fr;
  }

  &__logo1 {
    & a {
      display: block;
    }

    & img {
      display: block;
      max-width: 100%;
    }
  }

  &__logo2 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding-left: 34px;
  }

  &__logo3 {
    display: block;
    width: 100%;
    max-width: 149px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 55px;
    position: relative;
  }

  &__right {
    position: relative;
  }

  &__search {
    position: absolute;
    right: 116px;
    bottom: 0;
  }

  &__cart {
    position: absolute;
    right: 0;
    bottom: 0;
  }

  &__inner {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
  }

  &__buttons1 {
    display: flex;
    align-items: center;
    justify-content: flex-start;

    .btn {
      font-size: 14px;
      font-weight: 400;
    }

    .btn_call {
      border: 1px solid var(--theme-color-accent);
      @media (max-width: 1300px) {
        order: 3;
      }
    }
    /*
    & > *:not(:last-child, .header__login, .header__favorites) {
      margin-right: 10px;
    }
      */
  }

  &__burger {
    display: none;
  }

  &__email {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    margin: 0px 30px 0px 0px;
    transition: color 0.3s ease;
    @media (max-width: 1400px) {
      margin: 0px 10px 0px 0px;
    }
    @media (max-width: 1300px) {
      order: 2;
      margin: 0px 0px 20px 0px;
    }
    svg {
      margin: 2px 7px 0px 0px;
      fill: var(--theme-color-accent);
    }
    &:hover {
      color: var(--theme-color-accent);
    }
  }

  &__phones {
    margin: 0px 26px 0px 0px;
    display: flex;
    @media (max-width: 1400px) {
      margin: 0px 10px 0px 0px;
    }
    @media (max-width: 1300px) {
      order: 1;
      margin: 0px 0px 23px 0px;
    }
    svg {
      fill: var(--theme-color-accent);
      margin: 0px 7px 0px 0px;
    }
    &:hover {
      .header__phone {
        color: var(--theme-color-accent);
      }
    }
  }

  &__phone {
    position: relative;
    display: flex;
    flex-direction: column;
    font-weight: 700;
    font-size: 14px;
    line-height: 100%;
    transition: color 0.3s ease;
    span {
      position: absolute;
      top: 100%;
      left: 0;
      font-weight: 400;
      font-size: 12px;
      color: #9f9fa0;
      display: block;
      margin: 4px 0px 0px 0px;
      @media (max-width: 1300px) {
        top: 0;
        position: relative;
      }
    }
  }

  &__login {
    margin-right: 68px;
  }

  &__favorites {
    margin-right: 58px;
  }

  @media (max-width: 1460px) {
    &__bottom {
      padding-left: 0;
    }

    &__nav2 {
      flex-grow: 1;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  @media (max-width: 1300px) {
    padding-top: 12px;
    padding-bottom: 12px;

    &__nav2,
    &__nav1 {
      display: block;
      width: 100%;
    }

    &__buttons1 {
      flex-direction: column;
      width: 100%;
      margin: 25px 0px 0px 0px;

      .dropdown > .btn:not(.header__favorites) {
        width: 100%;
      }

      & > *:not(.header__favorites) {
        width: 100%;
        /*
        &:not(:last-child) {
          margin-right: 0;
          margin-bottom: 5px;
        }*/
      }
    }

    &__top,
    &__bottom {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }

    &__top {
      order: 2;
    }

    &__bottom {
      order: 1;

      .header__buttons1 {
        flex-direction: row;
        position: relative;
        padding-bottom: 16px;
        order: 1;

        .btn {
          --height: 38px;
        }

        &::after {
          content: "";
          height: 1px;
          border-bottom: 1px #dee0e0 solid;
          position: absolute;
          left: calc(-1 * var(--container-padding));
          width: calc(100% + var(--container-padding) + var(--container-padding));
          bottom: 0;
        }

        .header__favorites {
          flex-shrink: 0;
          margin: 0 0 0 15px;

          svg {
            width: 19px;
          }
        }

        .header__login {
          flex-grow: 1;
          margin: 0;
        }
      }

      .header__nav2 {
        order: 2;
        padding: 22px 0 27px 0;
      }
    }

    &__burger {
      display: block;
    }

    &__search {
      position: relative;
      right: 0;
    }

    &__cart {
      position: relative;
    }

    &__right {
      display: flex;
      justify-content: flex-end;
      align-items: center;

      & > *:not(.header__inner, .header__burger) {
        margin-left: 10px;
      }

      & > .header__burger {
        margin-left: 18px;
      }
    }

    &__inner {
      position: absolute;
      background-color: #fff;
      right: calc(-1 * var(--container-padding));
      top: calc(100% + 13px);
      width: calc(270px + var(--container-padding) + var(--container-padding));
      padding: 15px var(--container-padding) var(--container-padding) var(--container-padding);
      border-bottom-left-radius: 20px;
      display: none;
    }

    &__inner.header__inner_active {
      display: flex;
    }

    &__logo1 {
      width: 80px;
    }

    &__logo2 {
      padding-left: 20px;
    }

    &__logo3 {
      max-width: 120px;
    }

    .btn-catalog {
      --height: 40px;

      padding-left: 10px;
      padding-right: 15px;
      font-size: 16px;

      svg {
        width: 22px;
        display: block;
      }

      span {
        margin-left: 10px;
      }
    }

    & .dropdown__dropdown {
      top: unset;
      bottom: 0;
      padding-top: 6px;
      padding-bottom: 26px;
    }
  }

  @media (max-width: 900px) {
    .header__search > .btn {
      --height: 38px;

      svg {
        width: 14px;
      }
    }

    .header__search .header-search__inner .btn {
      --height: 32px;

      top: 3px;
      right: 3px;

      svg {
        width: 14px;
      }
    }

    .header__cart {
      --height: 38px;

      svg {
        width: 16px;
      }

      .btn__count {
        top: -3px;
        right: -3px;
      }
    }
  }

  @media (max-width: 768px) {
    &__logo1 {
      width: 60px;
    }

    &__logo2 {
      padding-left: 18px;
    }

    &__logo3 {
      max-width: 82px;
    }

    .btn-catalog {
      --height: 27px;

      padding-left: 7px;
      padding-right: 10px;
      font-size: 9px;

      svg {
        width: 9px;
        display: block;
      }

      span {
        margin-left: 6px;
      }
    }
  }

  @media (max-width: 500px) {
    &.header_search-active {
      .header__logo1,
      .header__logo2 {
        opacity: 0;
      }
    }
  }

  @media (max-width: 400px) {
    &.header_search-active {
      .header__cart {
        opacity: 0;
      }

      .btn-burger-close[data-action="hideMenuMobile"],
      .btn-burger {
        display: none;
      }

      .btn-burger-close[data-action="hideSearch"] {
        display: inline-block;
      }
    }
  }
}

.menu1 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0 -15px;

  li {
    margin: 0 15px;
  }

  a {
    font-size: 14px;

    &:hover {
      color: var(--theme-color-accent);
    }
  }

  @media (max-width: 1460px) {
    margin: 0 -7px;

    & > li {
      margin: 0 7px;
    }
  }

  @media (max-width: 1360px) {
    a {
      font-size: 12px;
    }
  }

  @media (max-width: 1300px) {
    display: block;
    margin: 0 0 28px 0;
    padding-top: 22px;
    border-top: 1px #dee0e0 solid;

    li {
      margin: 0;

      &:not(:last-child) {
        margin-bottom: 14.4px;
      }
    }

    a {
      font-size: 14px;
    }
  }
}

.menu2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0 -15px;

  & > li {
    margin: 0 15px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    min-height: 48px;

    & > a {
      font-size: 16px;
      text-transform: uppercase;
    }

    &.menu2__sub::after {
      content: "";
      margin-left: 8px;
      width: 12px;
      height: 8px;
      background-image: url("data:image/svg+xml,%3Csvg width='12' height='8' viewBox='0 0 12 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.862 0.862305L11.8048 1.80511L6.00005 7.60991L0.195312 1.80511L1.13812 0.862305L6.00005 5.72423L10.862 0.862305Z' fill='%233F3F40'/%3E%3C/svg%3E%0A");
    }

    &:hover > a {
      // color: var(--theme-color-accent);
    }

    &:hover > .menu2__submenu {
      display: block;
      visibility: visible;
    }

    &.menu2__sub:hover::after {
      transform: rotate(180deg);
    }
  }

  &__submenu {
    position: absolute;
    top: 100%;
    left: -40px;
    visibility: hidden;
    display: none;
    padding-top: 17px;

    ul {
      padding: 30px 40px;
      background-color: #fff;

      & > li {
        white-space: nowrap;
        position: relative;
        padding-right: 49px;

        &:not(:last-child) {
          margin-bottom: 20px;
        }

        & > a {
          font-size: 16px;
          font-weight: 600;
          color: inherit;

          &::after {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            width: 19px;
            height: 18px;
            background-image: url("data:image/svg+xml,%3Csvg width='19' height='18' viewBox='0 0 19 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M15.0498 10.0336H0V7.96635H15.0498L8.55674 1.46178L10.0159 0L19 8.99999L10.0159 18L8.55674 16.5382L15.0498 10.0336Z' fill='%233F3F40'/%3E%3C/svg%3E%0A");
          }

          &:hover {
            color: var(--theme-color-accent);
          }

          &:hover::after {
            background-image: url("data:image/svg+xml,%3Csvg width='19' height='18' viewBox='0 0 19 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M15.0498 10.0336H0V7.96635H15.0498L8.55674 1.46178L10.0159 0L19 8.99999L10.0159 18L8.55674 16.5382L15.0498 10.0336Z' fill='%231466B9'/%3E%3C/svg%3E%0A");
          }
        }
      }
    }
  }

  @media (max-width: 1460px) {
    margin: 0 -7px;

    & > li {
      margin: 0 7px;
    }
  }

  @media (max-width: 1360px) {
    & > li {
      & > a {
        font-size: 14px;
      }
    }
  }

  @media (max-width: 1300px) {
    display: block;
    margin: 0;
    width: 100%;

    & > li {
      margin: 0;
      min-height: 0;
      width: 100%;
      display: block;

      &:not(:last-child) {
        margin-bottom: 18px;
      }

      &.menu2__sub {
        padding-right: 20px;
      }

      &.menu2__sub::after {
        margin: 0;
        position: absolute;
        right: 0;
        top: 6px;
      }

      & > a {
        font-size: 14px;
      }
    }

    &__submenu {
      position: relative;
      top: 0;
      left: 0;
      display: none;
      padding-top: 0;

      ul {
        padding: 18px 0 0 0;

        & > li {
          white-space: normal;
          position: relative;
          padding-right: 0;

          &:not(:last-child) {
            margin-bottom: 17px;
          }

          & > a {
            font-size: 14px;
            font-weight: 600;

            &::after {
              display: none;
            }
          }
        }
      }
    }
  }
}

.header-search {
  &__inner {
    position: absolute;
    right: 0;
    top: 0;
    left: 0;
    height: 100%;
    opacity: 0;
    pointer-events: none;

    @include transition(opacity var(--theme-animation-duration-fast), left var(--theme-animation-duration));

    form {
      height: 100%;
      width: 100%;
    }

    .btn {
      --height: 38px;

      position: absolute;
      top: 5px;
      right: 5px;
    }
  }

  &__input {
    width: 100%;
    height: 100%;
    background-color: #fff;
    border: 1px #dee0e0 solid;
    border-radius: 24px;
    font-size: 14px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 5px 30px 5px 20px;
    color: var(--theme-color-text-common);

    &:focus-visible::placeholder {
      color: transparent;
    }
  }

  &__dropdown {
    position: absolute;
    left: 0;
    top: calc(100% + 5px);
    background-color: #fff;
    border-radius: 24px;
    border: 1px #dee0e0 solid;
    width: 100%;
    padding: 20px 25px;
    display: none;

    &.header-search__dropdown_active {
      display: block;
    }
  }

  &__nofound {
    font-size: 14px;
  }

  &__results {
    font-size: 14px;
    line-height: 1.5;

    li:not(:last-child) {
      margin-bottom: 10px;
    }

    a {
      display: inline-flex;
      justify-content: flex-start;
      align-items: center;

      img {
        display: block;
        width: 38px;
        height: 38px;
        margin-right: 15px;
        object-fit: contain;
        object-position: center;
      }

      &:hover {
        color: var(--theme-color-accent);
      }
    }
  }

  &.header-search_active {
    z-index: 1;
  }

  &.header-search_active &__inner {
    left: -992px;
    opacity: 1;
    pointer-events: all;
  }

  @media (max-width: 1580px) {
    &.header-search_active &__inner {
      left: calc(-1 * (100vw - 588px));
    }
  }

  @media (max-width: 1300px) {
    &.header-search_active &__inner {
      left: calc(-1 * (100vw - 480px));
    }
  }

  @media (max-width: 768px) {
    &.header-search_active &__inner {
      left: calc(-1 * (100vw - 350px));
    }
  }

  @media (max-width: 575px) {
    &.header-search_active &__inner {
      left: calc(-1 * (100vw - 233px));
    }
  }

  @media (max-width: 500px) {
    &.header-search_active &__inner {
      left: calc(-1 * (100vw - 165px));
    }
  }

  @media (max-width: 400px) {
    &.header-search_active &__inner {
      right: -58px;
    }
  }
}

.header__products {
  &.active {
    .menu-block {
      left: 0;
    }
  }
  .menu-block {
    border: none;
    position: fixed;
    top: 0;
    left: -100%;
    width: 555px;
    height: 100%;
    transition: left 0.3s ease;
    z-index: 50;
    border-radius: 0px;
    box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.15);
    background: #fff;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }

    @media (max-width: 600px) {
      width: 100%;
    }
  }
  .menu-block__body {
    display: block;
    padding: 0px 30px 30px 30px;
    position: relative;
    top: 0;
    border: none;

    a {
      svg {
        transform: rotate(90deg);
        @media (max-width: 992px) {
          transform: rotate(0deg);
        }
      }
      &._spoller-active {
        svg {
          transform: rotate(-90deg);
          @media (max-width: 992px) {
            transform: rotate(90deg);
          }
        }
      }
    }
    @media (max-width: 992px) {
      padding: 20px 15px 20px 15px;
    }
  }
  .menu-block__items {
    position: relative;
    left: 0;
    top: 0;
    border: none;
    width: 100%;
    padding: 5px 0px 30px 0px;
    @media (max-width: 992px) {
      padding: 0px 0px 30px 15px;
    }
    ul {
      gap: 20px 0px;
      @media (max-width: 992px) {
        gap: 16px 0px;
      }
    }
  }
  .menu-block__subitems {
    position: relative;
    left: 0;
    top: 0;
    border: none;
    width: 100%;
    padding: 20px 20px 0px 30px;
    @media (max-width: 992px) {
      padding: 15px 0px 5px 0px;
    }
    ul {
      gap: 16px 0px;
    }
  }
  .menu-block__title {
    display: flex;
    justify-content: flex-end;
    border: none;
    padding: 15px 15px 20px 15px;
    span {
      display: none;
      @media (max-width: 992px) {
        display: block;
        font-weight: 400;
        font-size: 25px;
        line-height: 100%;
        text-transform: uppercase;
      }
    }
    @media (max-width: 992px) {
      justify-content: space-between;
      border-bottom: 1px solid #dee0e0;
    }
  }
  .menu-block__item {
    padding: 0px 0px 0px 0px;
    border-bottom: 1px solid #dee0e0;
    &:last-child {
      border-bottom: none;
    }
    &:first-child {
      .menu-block__link {
        padding: 0px 0px 15px 0px;
      }
    }
    &:last-child {
      .menu-block__link {
        padding: 15px 0px 0px 0px;
      }
    }
    .menu-block__link {
      padding: 15px 0px;
    }
  }
  .menu-block__sublink {
    padding: 0px 0px 0px 0px;
  }
}
