.breadcrumbs {
  position: relative;
  margin: 30px 0px 40px 0px;

  @media (max-width: 767px) {
    margin: 17px 0px 20px 0px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  &__item {
    position: relative;
    font-weight: 400;
    font-size: 14px;
    display: flex;
    align-items: center;

    @media (max-width: 767px) {
      font-size: 10px;
      color: var(--theme-color-text-common);

      // Скрываем все элементы, кроме предпоследнего
      &:not(:nth-last-child(2)) {
        display: none;
      }

      // Оставляем видимым предпоследний элемент
      &:nth-last-child(2) {
        display: flex;
      }
    }

    &:not(:last-child) {
      margin-right: 12px;

      &::after {
        content: "";
        display: none;

        @media (min-width: 767px) {
          display: flex;
          margin-left: 12px;
          width: 16px;
          height: 1px;
          background: #3f3f40;
        }
      }

      a {
        &::before {
          content: "";
          display: none;
          width: 4px;
          height: 7px;
          margin: 0px 7px 0px 0px;
          transition: background-image 0.3s ease;
          background-image: url("data:image/svg+xml,%3Csvg width='4' height='7' viewBox='0 0 4 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.45436 3.5L4 6.22234L3.27282 7L-1.70858e-07 3.5L3.27282 8.90102e-07L4 0.777656L1.45436 3.5Z' fill='%233F3F40'/%3E%3C/svg%3E");

          @media (max-width: 767px) {
            display: block;
          }
        }
      }
    }

    &.is-active {
      color: inherit;
      color: var(--theme-color-accent);

      @media (max-width: 767px) {
        display: none;
      }
    }

    > a {
      display: flex;
      align-items: center;
      color: inherit;
      text-decoration: none;
      transition: all 0.25s ease;

      &:hover {
        color: var(--theme-color-accent);

        &::before {
          @media (max-width: 767px) {
            background-image: url("data:image/svg+xml,%3Csvg width='4' height='7' viewBox='0 0 4 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.45436 3.5L4 6.22234L3.27282 7L-1.70858e-07 3.5L3.27282 8.90102e-07L4 0.777656L1.45436 3.5Z' fill='%231466B9'/%3E%3C/svg%3E");
          }
        }
      }
    }
  }
}

.block-intro {
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: #f7f7f7;

  &__grid {
    display: grid;
    grid-template-columns: 1.4fr 1fr;
    gap: 30px 20px;
  }

  &__slider1 {
    min-width: 0;
  }

  &__slider2 {
    min-width: 0;
  }

  &__info {
    grid-column: span 2;
  }

  @media (max-width: 1100px) {
    &__grid {
      grid-template-columns: 1.3fr 1fr;
    }
  }

  @media (max-width: 1000px) {
    background: none;

    &__grid {
      grid-template-columns: 1fr;
      gap: 20px;
    }

    &__slider1 {
      order: 1;
    }

    &__slider2 {
      order: 3;
    }

    &__info.grid1 {
      order: 2;
      grid-column: auto;
      grid-template-columns: 1fr 1fr;
    }
  }

  @media (max-width: 640px) {
    &__grid {
      overflow: hidden;
    }

    &__slider1 {
      padding-bottom: 34px;
    }

    &__slider2 {
      padding-bottom: 24px;
    }

    &__info.grid1 {
      gap: 15px;
    }
  }

  @media (max-width: 500px) {
    &__info.grid1 {
      gap: 10px;
    }
  }

  @media (max-width: 400px) {
    &__info.grid1 {
      gap: 5px;
    }
  }
}

.block-feedback {
  &__inner {
    background-color: #f7f7f7;
    border-radius: 20px;
    padding: 100px 108px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 100px;

    hr {
      border: none;
      border-top: 1px #dee0e0 solid;
      margin: 30px 0;
    }
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
  }

  &__left {
    margin-top: -7px;
  }

  &__phone {
    font-size: 24px;
    font-weight: 600;
    line-height: 1.5;
    margin-bottom: 16px;
  }

  .socials {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: -6px;
    margin-right: -6px;

    & > * {
      margin: 0 6px;
    }

    span:not(.btn) {
      position: absolute;
      left: -10000px;
      top: auto;
      width: 1px;
      height: 1px;
      overflow: hidden;
    }
  }

  .title2 {
    font-size: 40px;
    line-height: 1.1;
    margin-bottom: 13px;

    span {
      color: var(--theme-color-accent);
    }
  }

  @media (max-width: 1280px) {
    &__inner {
      padding: 60px;
      gap: 50px;
    }

    .title2 {
      font-size: 35px;
    }
  }

  @media (max-width: 1100px) {
    .title2 {
      font-size: 30px;
    }
  }

  @media (max-width: 1000px) {
    &__inner {
      padding: 40px;
      gap: 40px;
    }
  }

  @media (max-width: 900px) {
    .container {
      padding: 0;
    }

    &__inner {
      padding: 43px var(--container-padding);
      border-radius: 0;
    }

    .title2 {
      font-size: 28px;
    }

    &__text {
      font-size: 14px;
    }
  }

  @media (max-width: 768px) {
    &__inner {
      grid-template-columns: 1fr;
      gap: 50px;
    }

    .title2 {
      font-size: 26px;
    }

    hr {
      margin: 20px 0;
    }

    &__phone {
      font-size: 22px;
    }
  }

  @media (max-width: 640px) {
    .title2 {
      font-size: 25px;
    }

    &__phone {
      font-size: 20px;
    }
  }

  @media (max-width: 500px) {
    &__phone {
      font-size: 18px;
    }
  }
}

.block-news-mp {
  .swiper-navbar {
    display: none;
  }

  @media (max-width: 640px) {
    .heading-complex__right .swiper-navs {
      display: none;
    }

    .swiper-navbar {
      display: flex;
    }
  }
}

.block-contacts {
  margin: 40px 0px;
  @media (max-width: 767px) {
    margin: 20px 0px 70px 0px;
  }

  &__content {
    border: 1px solid #dee0e0;
    border-radius: 10px;
    padding: 40px;
    @media (max-width: 991px) {
      padding: 30px 20px 20px 20px;
    }
  }

  &__body {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: -10px;
    @media (max-width: 650px) {
      flex-direction: column;
      justify-content: start;
      margin: -10px 0px;
    }
  }

  &__column {
    flex: 0 1 auto;
    padding: 10px;
    @media (max-width: 991px) {
      flex: 0 1 50%;
    }
    @media (max-width: 650px) {
      flex: 1 1 auto;
      padding: 10px 0px;
    }
  }

  &__title {
    font-weight: 400;
    font-size: 16px;
    color: rgba(63, 63, 64, 0.5);
    margin: 0px 0px 16px 0px;
    @media (max-width: 480px) {
      font-size: 12px;
      margin: 0px 0px 5px 0px;
    }
  }

  &__phone {
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    transition: color 0.3s ease 0s;
    &:hover {
      color: var(--theme-color-accent);
    }
    @media (max-width: 480px) {
      font-size: 18px;
    }
  }

  &__email {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--theme-color-accent);
    transition: color 0.3s ease 0s;
    &:hover {
      color: var(--theme-color-accent-80);
    }
    @media (max-width: 480px) {
      font-size: 14px;
    }
  }

  &__works {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    margin: -4px 0px;
    @media (max-width: 480px) {
      font-size: 14px;
      margin: -2.5px 0px;
    }
    span {
      display: block;
      padding: 4px 0px;
      @media (max-width: 480px) {
        padding: 2.5px 0px;
      }
    }
  }
}

.heading-brands {
  @media (max-width: 768px) {
    margin-bottom: 20px;
  }
  .title2 {
    line-height: 120%;
    @media (max-width: 767px) {
      line-height: 1;
    }
  }
}
.block-brands {
  padding: 8px 0px 0px 0px;
  @media (max-width: 767px) {
    padding: 0px 0px 0px 0px;
  }
  &__body {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    @media (max-width: 1100px) {
      grid-template-columns: 1fr;
      gap: 28px 0px;
    }
  }

  &__column {
    border-radius: 10px;
    background: #f7f7f7;
    padding: 30px;
    display: flex;
    flex-direction: column;
    height: 100%;
    @media (max-width: 767px) {
      padding: 10px;
    }
    .descr {
      padding: 30px;
      display: flex;
      flex-direction: column;
      height: 100%;
      @media (max-width: 767px) {
        padding: 15px 10px 10px 10px;
      }
      h3 {
        line-height: 150%;
        text-transform: uppercase;
        margin: 0px 0px 20px 0px;
        @media (max-width: 767px) {
          margin: 0px 0px 10px 0px;
        }
      }
      h4 {
        font-weight: 600;
        margin: 0px 0px 20px 0px;
        @media (max-width: 767px) {
          font-size: 14px;
          margin: 0px 0px 10px 0px;
        }
      }
      ul {
        flex: 1 1 auto;
        margin: 0px 0px 40px 0px;
        @media (max-width: 767px) {
          margin: 0px 0px 20px 0px;
        }
        li {
          margin: 0px 0px 12px 0px;
          &:last-child {
            margin: 0px 0px 0px 0px;
          }
        }
      }
    }
  }

  &__photo {
    border-radius: 10px;
    background-color: #fff;
    min-height: 314px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px;
    @media (max-width: 767px) {
      min-height: 132px;
    }
    img {
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
    .logo1 {
      height: 226px;
      @media (max-width: 767px) {
        height: 108px;
      }
    }
    .logo2 {
      height: 115px;
      @media (max-width: 767px) {
        height: 48px;
      }
    }
  }

  &__buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    @media (max-width: 600px) {
      font-size: 14px;
      gap: 10px;
    }
    .btn {
      height: 60px;
      text-transform: uppercase;
      line-height: 1;
      padding: 5px 30px;
      border-radius: 47px;
      @media (max-width: 600px) {
        flex: 1 1 auto;
        height: 50px;
        padding: 3px 30px;
      }
    }
    .btn_hollow {
      border: 1px solid var(--theme-color-accent);
    }
    .btn-brands {
      padding: 5px 5px 5px 30px;
      @media (max-width: 600px) {
        justify-content: space-between;
        padding: 3px 3px 3px 30px;
      }
      span {
        border-radius: 50%;
        background-color: #fff;
        width: 50px;
        flex: 0 0 50px;
        height: 50px;
        margin: 0px 0px 0px 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        @media (max-width: 600px) {
          width: 44px;
          height: 44px;
          flex: 0 0 44px;
          margin: 0px 0px 0px 20px;
        }
      }
      & svg {
        fill: var(--theme-color-accent);
      }
    }
  }
}
.block-brand {
  .block-brands__photo {
    @media (max-width: 767px) {
      min-height: 262px;
    }
  }
  .block-brands__column {
    display: grid;
    grid-template-columns: minmax(0, 690px) minmax(0, 710px);
    gap: 30px;
    @media (max-width: 1100px) {
      grid-template-columns: 1fr;
      gap: 0px;
    }
    .descr {
      padding: 30px 0px 0px 30px;
      @media (max-width: 1100px) {
        padding: 30px 20px 20px 20px;
      }
      @media (max-width: 767px) {
        padding: 7px 10px 10px 10px;
      }
      h3 {
        @media (max-width: 767px) {
          margin: 0px 0px 20px 0px;
        }
      }
      ul {
        margin: 0px 0px 20px 0px;
      }
      p {
        font-family: "Inter", sans-serif;
      }
    }
  }
}

.block-catalogs {
  padding: 8px 0px 0px 0px;
  @media (max-width: 767px) {
    padding: 3px 0px 0px 0px;
  }

  &__body {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px 20px;
    @media (max-width: 1550px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: 1220px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 650px) {
      grid-template-columns: 1fr;
      gap: 15px 0px;
    }
  }
}
.block-catalogs-detail {
  .block-catalogs__title {
    @media (max-width: 767px) {
      min-height: 70px;
    }
  }
}

.block {
  padding: 6px 0px 0px 0px;
  @media (max-width: 767px) {
    padding: 0px 0px 0px 0px;
  }
  .descr {
    margin: 100px 0px 0px 0px;
    @media (max-width: 767px) {
      margin: 70px 0px 0px 0px;
    }
    p {
      margin: 0px 0px 24px 0px;
      @media (max-width: 767px) {
        margin: 0px 0px 20px 0px;
      }
    }
    ul {
      margin: 0px 0px 24px 0px;
      @media (max-width: 767px) {
        margin: 0px 0px 20px 0px;
      }
      li {
        margin: 0px 0px 0px 0px;
        &::before {
          content: "";
          width: 5px;
          height: 5px;
          flex: 0 0 5px;
          background-color: var(--theme-color-text-common);
          margin: 10px 10px 0px 10px;
        }
      }
    }
  }
  &__content {
    display: flex;
    align-items: start;
    justify-content: space-between;
    gap: 42px;
    @media (max-width: 992px) {
      flex-direction: column;
      justify-content: start;
      gap: 20px 0px;
    }
  }

  &__menu {
    flex: 0 1 345px;
    border: 1px solid #dee0e0;
    border-radius: 10px;
    padding: 24px 30px;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    @media (max-width: 992px) {
      flex: 1 1 auto;
      width: 100%;
      padding: 0px 0px 0px 0px;
    }
    @media (max-width: 767px) {
      font-size: 14px;
    }
    span {
      display: flex;
      justify-content: space-between;
      align-items: center;
      &.active {
        color: var(--theme-color-accent);
        @media (max-width: 992px) {
          padding: 15px 12px 14px 15px;
          cursor: pointer;
          color: var(--theme-color-text-common);
          &::after {
            content: "";
            width: 13px;
            height: 8px;
            margin: 0px 0px 0px 10px;
            transition: background-image 0.3s ease;
            background-image: url("data:image/svg+xml,%3Csvg%20width='13'%20height='8'%20viewBox='0%200%2013%208'%20fill='none'%20xmlns='http://www.w3.org/2000/svg'%3E%3Cpath%20d='M6.36256%204.95L11.3126%200L12.7266%201.414L6.36256%207.778L-0.00143814%201.414L1.41256%200L6.36256%204.95Z'%20fill='%233F3F40'/%3E%3C/svg%3E");
            background-repeat: no-repeat;
            background-size: contain;
          }
        }
      }
      &._spoller-active {
        &::after {
          content: "";
          transform: rotate(-180deg);
        }
      }
    }
    ul {
      display: grid;
      gap: 16px 0px;
      @media (max-width: 992px) {
        gap: 14px 0px;
      }
      a {
        &:hover {
          color: var(--theme-color-accent);
        }
      }
    }
  }

  &__submenu {
    margin: 16px 0px 0px 0px;
    @media (max-width: 992px) {
      padding: 0px 15px 15px 15px;
      margin: 0px 0px 0px 0px;
    }
  }

  &__info {
    flex: 0 1 1135px;
    @media (max-width: 992px) {
      flex: 1 1 auto;
    }
    .heading-complex {
      margin: 0px 0px 30px 0px;
      @media (max-width: 767px) {
        margin: 0px 0px 20px 0px;
      }
      p {
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        margin: 20px 0px 0px 0px;
        @media (max-width: 767px) {
          font-size: 14px;
        }
      }
    }
    p {
      font-family: "Inter", sans-serif;
    }
  }

  &__catalogs {
    flex: 0 1 1135px;
    display: grid;
    gap: 20px;
    grid-template-columns: 1fr 1fr;
    @media (max-width: 992px) {
      flex: 1 1 auto;
    }
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
      gap: 15px 0px;
      width: 100%;
    }
  }

  &__photo {
    max-height: 400px;
    width: 100%;
    object-fit: cover;
    margin: 0px 0px 30px 0px;
    border-radius: 10px;
    min-height: 120px;
    @media (max-width: 992px) {
      border-radius: 0px;
      margin: 0px calc(-1 * var(--container-padding)) 30px calc(-1 * var(--container-padding));
      width: calc(100% + (2 * var(--container-padding)));
    }
    @media (max-width: 767px) {
      margin: 0px calc(-1 * var(--container-padding)) 15px calc(-1 * var(--container-padding));
    }
  }

  &__filters {
    display: grid;
    flex: 0 1 345px;
    gap: 20px 0px;
    @media (max-width: 992px) {
      flex: 1 1 auto;
      width: 100%;
      gap: 10px;
      display: flex;
      height: 100%;
    }
    .menu-block__title {
      @media (max-width: 992px) {
        padding: 14px;
      }
      svg {
        @media (max-width: 767px) {
          width: 9px;
          height: 9px;
        }
      }
    }
    .menu-block.active .menu-block__title svg {
      transform: rotate(0deg);
    }
  }

  &__top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
    margin: 0px 0px 20px 0px;
    @media (max-width: 992px) {
      flex-direction: column;
      justify-content: start;
      align-items: start;
    }
    @media (max-width: 767px) {
      margin: 0px 0px 30px 0px;
    }
    p {
      font-weight: 400;
      font-size: 12px;
      line-height: 150%;
    }
  }

  &__image {
    width: auto;
    max-height: 136px;
    @media (max-width: 992px) {
      max-width: 100%;
      flex: 1 1 auto;
    }
  }

  &__tables {
    position: sticky;
    top: 0;
    z-index: 10;
    background-color: #fff;
    flex: 1 1 auto;
    display: flex;
    margin: 0px 0px 6px 0px;
    border-radius: 10px;
    border: 1px solid #dee0e0;
    @media (max-width: 1550px) {
      display: none;
    }
  }

  &__table {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 1 3.4%;
    font-weight: 700;
    font-size: 10px;
    line-height: 150%;
    text-align: center;
    color: #8d8d8d;
    border-right: 1px solid #f4f4f4;
    padding: 10px 5px;
    &:last-child {
      border-right: none;
    }
  }

  &__product-catalogs {
    display: grid;
    gap: 6px;
    @media (max-width: 1550px) {
      grid-template-columns: 1fr 1fr;
      align-items: start;
    }
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
      gap: 10px 0px;
    }
  }
}

.filter-block {
  border: 1px solid #dee0e0;
  border-radius: 10px;
  @media (max-width: 992px) {
    border: none;
    border-radius: 0px;
    flex: 0 1 30%;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    border-bottom: 1px solid #dee0e0;
    padding: 24px 30px;
    svg {
      margin: 0px 0px 0px 20px;
      @media (max-width: 767px) {
        width: 9px;
        height: 9px;
      }
    }
    @media (min-width: 992px) {
      pointer-events: none;
    }
    @media (max-width: 992px) {
      background: #f7f7f7;
      height: 100%;
      border: none;
      border-radius: 10px;
      padding: 15px 16px;
    }
    @media (max-width: 767px) {
      font-weight: 500;
      font-size: 14px;
      line-height: 150%;
    }
