        color: var(--theme-color-accent-80);
      }
    }
  }
  .container {
    max-width: 860px;
    padding: 0px 15px;
    margin: 0 auto;
  }
}

.personal-cabinet-block {
  .form {
    align-items: start;
    max-width: 1035px;
    margin: 0px 0px 40px 0px;
    &:last-child {
      margin: 0px 0px 0px 0px;
    }
    @media (max-width: 768px) {
      margin: 0px 0px 30px 0px;
    }
  }

  .form__column {
    width: 100%;
  }
  .btn {
    min-width: 305px;
    font-size: 18px;
    text-transform: none;
    --height: 48px;
    width: auto;
    padding: 5px 55px;
    @media (max-width: 480px) {
      width: 100%;
      padding: 5px 25px;
    }
  }
  .other-form textarea.field-input1 {
    height: 122px;
  }
}

.personal-cabinet-tabs {
  &__navigation {
    gap: 0px 60px;
    display: flex;
    margin: 0px 0px 30px 0px;
    border-bottom: 1px solid #d9d9d9;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }
    @media (max-width: 1520px) {
      margin: 0px -15px 30px -15px;
      padding: 0px 15px;
    }
    @media (max-width: 768px) {
      gap: 0px 20px;
    }
  }

  &__title {
    white-space: nowrap;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    transition: color 0.3s;
    padding: 0px 0px 12px 0px;
    &._tab-active {
      border-bottom: 3px solid var(--theme-color-accent);
      color: var(--theme-color-accent);
    }
    &:hover {
      color: var(--theme-color-accent);
    }
    @media (max-width: 768px) {
      font-size: 14px;
    }
  }

  &__pagging {
    max-width: 1035px;
  }

  &__body {
  }

  &__scroll {
  }

  &__tables {
    display: flex;
    flex-direction: column;
    gap: 5px;
    max-width: 1035px;
    @media (max-width: 1100px) {
      min-width: 100%;
    }
  }

  &__main-table {
    min-width: 100%;
    display: flex;
    flex-wrap: nowrap;
    border: 1px solid #dee0e0;
    border-radius: 10px;
    .personal-cabinet-tabs__value {
      font-weight: 700;
      color: #8d8d8d;
      text-align: left;
    }
    @media (max-width: 1100px) {
      display: none;
    }
  }

  &__value {
    display: flex;
    align-items: center;
    padding: 15px 13px;
    text-align: center;
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
    white-space: nowrap;
    border-right: 1px solid #f4f4f4;
    @media (max-width: 1100px) {
      flex-wrap: wrap;
      text-align: left;
      padding: 0px 0px 0px 0px;
    }
    span {
      font-weight: 700;
      color: #8d8d8d;
      display: none;
      margin: 0px 0px 2px 0px;
      @media (max-width: 1100px) {
        display: block;
        flex: 1 1 100%;
      }
    }
    svg {
      fill: #8d8d8d;
      margin: 0px 5px 0px 0px;
    }
    &.yellow {
      svg {
        fill: #f59e0b;
      }
    }
    &.blue {
      svg {
        fill: var(--theme-color-accent);
      }
    }
    &.green {
      svg {
        fill: #27ae60;
      }
    }
    &.red {
      svg {
        fill: #b00d0c;
      }
    }
    &:last-child {
      border-right: none;
    }
    &:nth-child(1) {
      flex: 0 0 8.5%;
      @media (max-width: 1100px) {
        flex: 0 1 50%;
        font-size: 25px;
      }
    }
    &:nth-child(2) {
      flex: 0 0 11.3%;
      @media (max-width: 1100px) {
        flex: 0 1 50%;
        text-align: right;
        justify-content: flex-end;
        align-items: flex-end;
        margin: 0px 0px 4px 0px;
      }
    }
    &:nth-child(3) {
      flex: 0 0 7.8%;
      @media (max-width: 1100px) {
        flex: 1 1 100%;
      }
    }
    &:nth-child(4) {
      flex: 0 0 13.4%;
      @media (max-width: 1100px) {
        flex: 1 1 100%;
      }
    }
    &:nth-child(5) {
      flex: 0 0 23.7%;
      @media (max-width: 1100px) {
        flex: 1 1 100%;
      }
    }
    &:nth-child(6) {
      flex: 0 0 21.2%;
      @media (max-width: 1100px) {
        flex: 1 1 100%;
      }
    }
    &:nth-child(7) {
      flex: 0 0 13.9%;
      @media (max-width: 1100px) {
        flex: 1 1 100%;
      }
    }
  }

  &__table {
    min-width: 100%;
    display: flex;
    border: 1px solid #dee0e0;
    border-radius: 10px;
    @media (max-width: 1100px) {
      flex-wrap: wrap;
      padding: 20px;
      gap: 10px 0px;
    }
    a {
      display: flex;
      align-items: center;
      font-weight: 600;
      font-size: 14px;
      line-height: 150%;
      text-transform: uppercase;
      color: var(--theme-color-accent);
      transition: color 0.3s;
      svg {
        fill: currentColor;
        margin: 0px 0px 0px 5px;
      }
      &:hover {
        color: var(--theme-color-accent-80);
      }
      @media (max-width: 1100px) {
        margin: 5px 0px 0px 0px;
        &:first-child {
          margin: 0px 0px 0px 0px;
        }
      }
    }
  }
}
