.tabs {
    &__navigation {
        display: flex;
        flex-wrap: wrap;
        gap: 12px;
        @media (max-width: 992px) {
            padding-right: var(--container-padding);
            margin: 0px calc(-1 * var(--container-padding)) 0px 0px;
            flex-wrap: nowrap;
            overflow: auto;
            -ms-overflow-style: none;
            scrollbar-width: none;
            &::-webkit-scrollbar {
                display: none;
            }
        }
    }

    &__title {
        background: #f7f7f7;
        flex: 0 1 auto;
        border-radius: 10px;
        padding: 24px 28px;
        font-weight: 400;
        font-size: 18px;
        line-height: 100%;
        text-transform: uppercase;
        text-align: center;
        transition: color 0.3s ease;
        &._tab-active {
            pointer-events: none;
            color: #fff;
            background: var(--theme-color-accent);
        }
        &:hover {
            color: var(--theme-color-accent);
        }
        @media (max-width: 767px) {
            font-size: 12px;
            padding: 14px 16.5px;
        }
    }

    &__content {
        margin: 30px 0px 0px 0px;
        @media (max-width: 767px) {
            margin: 20px 0px 0px 0px;
        }
    }

    &__body {
    }

    &__lists {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 0px 40px;
        border: 1px solid #dee0e0;
        border-radius: 10px;
        padding: 40px;
        background-color: #ffffff;
        @media (max-width: 992px) {
            gap: 16px 0px;
            padding: 0px 0px 0px 0px;
            grid-template-columns: 1fr;
            border: none;
        }
        ul {
            @media (max-width: 992px) {
                &:last-child {
                    li {
                        &:last-child {
                            border-bottom: none;
                            padding: 16px 0px 0px 0px;
                        }
                    }
                }
            }
            li {
                padding: 16px 0px;
                display: flex;
                border-bottom: 1px solid #dee0e0;
                &:first-child {
                    padding: 0px 0px 16px 0px;
                }
                @media (min-width: 992px) {
                    &:last-child {
                        padding: 16px 0px 0px 0px;
                        border-bottom: none;
                    }
                }
            }
        }
    }

    &__list-name {
        flex: 0 0 52.5%;
        font-weight: 400;
        font-size: 16px;
        color: rgba(63, 63, 64, 0.5);
        padding: 0px 10px 0px 0px;
        @media (max-width: 767px) {
            font-size: 12px;
            flex: 0 0 62.5%;
        }
    }

    &__list-value {
        flex: 0 0 47.5%;
        font-weight: 400;
        font-size: 16px;
        @media (max-width: 767px) {
            font-size: 12px;
            flex: 0 0 37.5%;
        }
    }
}
