@charset "UTF-8";
@font-face {
  font-family: Inter;
  src: url("../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Inter;
  src: url("../fonts/Inter-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Inter;
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Inter;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Poppins;
  src: url("../fonts/Poppins-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Poppins;
  src: url("../fonts/Poppins-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Poppins;
  src: url("../fonts/Poppins-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Poppins;
  src: url("../fonts/Poppins-Italic.woff2") format("woff2");
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}
:root {
  --container-width: 1520px;
  --main-outer-width: 1920px;
  --container-padding: 30px;
  --calculated-padding: calc((100% - var(--container-width)) / 2);
  --theme-color-accent: rgba(20, 102, 185, 1);
  --theme-color-accent-80: rgba(20, 102, 185, 0.8);
  --theme-color-grey: #a8a8aa;
  --theme-color-text-common: #3f3f40;
  --theme-border-color: #ececec;
  --theme-animation-duration: 200ms;
  --theme-animation-duration-fast: 100ms;
  --second-family: "Poppins", sans-serif;
  --third-family: "Noah", sans-serif;
}
@media (max-width: 767px) {
  :root {
    --container-padding: 20px;
  }
}
@media (max-width: 575px) {
  :root {
    --container-padding: 15px;
  }
}

/*
Псевдоэлемент-расширитель для кнопок:
1. Помогает попадать по небольшим кнопкам
2. Помогает отлавливать событие нажатия на кнопку при наличии в ней различных элементов
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

input,
button,
textarea {
  padding: 0;
  background: none;
  border: none;
  outline: none;
}

html,
body {
  height: 100%;
  padding: 0;
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

ul,
ol {
  list-style-type: none;
  padding: 0;
}

a {
  text-decoration: none;
}

.main-wrapper-outer {
  width: 100%;
  min-height: 100%;
  max-width: var(--main-outer-width);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
}

.header {
  flex-grow: 0;
  flex-shrink: 0;
}

.footer {
  flex-grow: 0;
  flex-shrink: 0;
}

.main {
  flex-grow: 1;
  flex-shrink: 1;
}
body:not(.mp) .main {
  padding-bottom: clamp(50px, 5vw, 100px);
}

.container {
  padding-left: max(var(--calculated-padding), var(--container-padding));
  padding-right: max(var(--calculated-padding), var(--container-padding));
}
.container__inner {
  width: 100%;
  position: relative;
}

.container.container_extract-l {
  padding-left: 0;
}

.container.container_extract-lp {
  padding-left: var(--container-padding);
}

.container.container_extract-r {
  padding-right: 0;
}

.container.container_extract-rp {
  padding-right: var(--container-padding);
}

.container.container_extract-lr {
  padding-left: 0;
  padding-right: 0;
}

.container.container_extract-lrp {
  padding-left: var(--container-padding);
  padding-right: var(--container-padding);
}

.container.container_blog {
  --calculated-padding: calc((100% - 1195px) / 2);
}

.form-grid1 {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: auto;
  gap: 14px 20px;
}
.form-grid1__row {
  grid-column: span 12;
}
.form-grid1__row_2 {
  grid-column: span 6;
}
.form-grid1__row_4 {
  grid-column: span 3;
}

.grid1 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

.block1 {
  margin-bottom: 100px;
}
@media (max-width: 1000px) {
  .block1 {
    margin-bottom: 80px;
  }
}
@media (max-width: 768px) {
  .block1 {
    margin-bottom: 70px;
  }
}
@media (max-width: 575px) {
  .block1.block-intro, .block1.block-feedback {
    margin-bottom: 0;
  }
}

.btn {
  --height: 48px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.25;
  padding: 5px 24px;
  border-radius: calc(var(--height) / 2);
  min-height: var(--height);
  position: relative;
  transition: background-color var(--theme-animation-duration);
}
.btn::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.btn svg + * {
  margin-left: 12px;
}
.btn span + svg {
  margin-left: 10px;
}
.btn_xsmall {
  font-size: 14px;
  min-height: 38px;
  border-radius: 5px;
}
.btn_small {
  --height: 36px;
  font-size: 14px;
  font-weight: 400;
}
.btn_big {
  --height: 60px;
  text-transform: uppercase;
  padding-left: 40px;
  padding-right: 40px;
}
.btn_wide {
  display: flex;
  width: 100%;
}
.btn_primary {
  background-color: var(--theme-color-accent);
  color: #fff;
}
.btn_primary.btn_hollow {
  background: none;
  border: 1.5px var(--theme-color-accent) solid;
  color: var(--theme-color-accent);
}
.btn_primary.btn_hollow:hover {
  background-color: var(--theme-color-accent);
  color: #fff;
}
.btn_primary svg {
  fill: #fff;
}
.btn_primary:not(.btn_hollow):focus-visible, .btn_primary:not(.btn_hollow):hover {
  background-color: var(--theme-color-accent-80);
}
.btn_white {
  background-color: #fff;
  color: inherit;
}
.btn_white.btn_hollow {
  background: none;
  border: 1px #fff solid;
  color: #fff;
}
.btn_white:not(.btn_hollow):hover {
  color: var(--theme-color-accent);
}
.btn_black {
  background-color: var(--theme-color-text-common);
  color: #fff;
}
.btn_black.btn_hollow {
  background: none;
  border: 1px var(--theme-color-text-common) solid;
  color: var(--theme-color-text-common);
}
.btn_grey {
  background-color: #dee0e0;
  color: #fff;
}
.btn_grey.btn_hollow {
  background: none;
  border: 1px #dee0e0 solid;
  color: inherit;
}
.btn_grey:hover:not(:disabled, .btn_hollow), .btn_grey:focus-visible:not(:disabled, .btn_hollow) {
  background-color: var(--theme-color-accent);
}
.btn_grey.btn_hollow:hover:not(:disabled), .btn_grey.btn_hollow:focus-visible:not(:disabled) {
  border-color: var(--theme-color-accent);
  color: var(--theme-color-accent);
}
.btn_grey:disabled {
  opacity: 0.5;
}
.btn_grey svg {
  fill: var(--theme-color-text-common);
}
.btn_grey:hover svg {
  fill: var(--theme-color-accent);
}
.btn_arrow1 {
  text-transform: uppercase;
}
.btn_arrow1 svg {
  transform: rotate(-45deg);
  margin-left: 8px;
}
.btn_arrow1:hover:not(:disabled) svg, .btn_arrow1:focus-visible:not(:disabled) svg {
  fill: var(--theme-color-accent);
}
.btn_circle {
  width: var(--height);
  padding: 0;
}
.btn_social.btn {
  --height: 34px;
  padding: 0;
}
.btn_social.btn svg {
  display: block;
}
.btn:disabled {
  cursor: default;
}
.btn__count {
  position: absolute;
  top: 0;
  right: 0;
  height: 18px;
  min-width: 18px;
  border-radius: 9px;
  color: #fff;
  font-size: 11px;
  font-weight: 400;
  line-height: 1;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0 5px;
  background-color: var(--theme-color-accent);
}
@media (max-width: 768px) {
  .btn {
    --height: 39px;
    font-size: 14px;
  }
  .btn:not(.btn_circle) {
    padding: 5px 20px;
  }
  .btn svg + * {
    margin-left: 8px;
  }
  .btn span + svg {
    margin-left: 8px;
  }
  .btn_big {
    --height: 52px;
    font-size: 14px;
    padding-left: 35px;
    padding-right: 35px;
  }
}
@media (max-width: 500px) {
  .btn {
    --height: 32px;
    font-size: 12px;
  }
  .btn:not(.btn_circle) {
    padding: 3px 15px;
  }
  .btn_arrow1 svg {
    max-height: 15px;
    margin-left: 7px;
  }
  .btn .nomobile {
    display: none;
  }
  .btn_big {
    --height: 44px;
    font-size: 12px;
    padding-left: 30px;
    padding-right: 30px;
  }
}

.dropdown {
  position: relative;
}
.dropdown > .btn {
  z-index: 1;
}
.dropdown__dropdown {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  border-radius: 18px;
  padding-top: 36px;
  border: 1px var(--theme-color-accent) solid;
  overflow: hidden;
  display: none;
}
.dropdown__dropdown-inner {
  background-color: #fff;
  padding: 6px 24px 14px 24px;
}
.dropdown__dropdown-inner > *:not(:last-child) {
  margin-bottom: 12px;
}
.dropdown.dropdown_active {
  z-index: 2;
}
.dropdown.dropdown_active .btn {
  border-color: transparent !important;
}
.dropdown.dropdown_active .dropdown__dropdown {
  display: block;
}
.dropdown .socials > * {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.dropdown .socials > *:not(:last-child) {
  margin-bottom: 12px;
}
.dropdown .socials a > span:not(.btn) {
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
}
.dropdown .socials .btn {
  --height: 20px;
  flex-shrink: 0;
  margin-right: 10px;
}
.dropdown .socials .btn svg {
  transform: scale(0.7);
}

.btn-catalog {
  font-size: 18px;
  font-weight: 400;
}
.btn-catalog svg {
  fill: #fff;
}
.btn-catalog svg + * {
  margin-left: 16px;
}

.btn-burger {
  width: 32px;
  height: 18px;
  position: relative;
}
.btn-burger svg {
  stroke: var(--theme-color-accent);
}
.btn-burger::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 32px;
  height: 32px;
}

.btn-burger-close {
  width: 32px;
  height: 18px;
  position: relative;
  display: none;
}
.btn-burger-close svg {
  stroke: var(--theme-color-accent);
}
.btn-burger-close::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 32px;
  height: 32px;
}

.form-group {
  position: relative;
}
.form-group__error {
  position: absolute;
  right: 5px;
  bottom: 3px;
  font-size: 12px;
  color: #b91414;
}
.form-group_error .field-input1, .form-group_error .field-input2 {
  border: 1px solid #b91414 !important;
}

.form-group1 {
  position: relative;
  background-color: #fff;
  border-radius: 10px;
}
.form-group1__icon {
  cursor: pointer;
  position: absolute;
  right: 20px;
  top: 46px;
}
.form-group1__icon svg {
  fill: #9f9fa0;
}
.form-group1__icon.hidden {
  display: none;
}
.form-group1__icon.open {
  top: 39px;
  right: 18px;
}
.form-group1__placeholder {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translate(0, -50%);
  color: #313c41;
  font-size: 14px;
  line-height: 1.25;
  pointer-events: none;
}
.form-group1__placeholder_req::after {
  content: "*";
  color: var(--theme-color-accent);
  margin-left: 3px;
}
.field-input1:focus-visible + .form-group1__placeholder, .field-input1:not(:placeholder-shown) + .form-group1__placeholder {
  opacity: 0;
}
textarea.field-input1 + .form-group1__placeholder {
  top: 17px;
  transform: none;
}

.form-group2 {
  position: relative;
}
.form-group2__error {
  position: absolute;
  right: 5px;
  bottom: 3px;
  font-size: 12px;
  color: #b91414;
}
.form-group2__label {
  display: block;
  color: rgba(63, 63, 64, 0.5);
  font-size: 16px;
  line-height: 1.25;
  margin-bottom: 8px;
}
.form-group2__label_req::after {
  content: "*";
  color: #cd1311;
  margin-left: 5px;
}
@media (max-width: 768px) {
  .form-group2__label {
    font-size: 14px;
    margin-bottom: 8px;
  }
}

.field-input1 {
  width: 100%;
  height: 52px;
  background: none;
  padding: 12px 20px;
  font-size: 14px;
  line-height: 1.25;
  border-radius: 10px;
  border: 1px transparent solid;
  color: var(--theme-color-text-common);
}

textarea.field-input1 {
  height: 108px;
  padding-top: 17px;
  resize: none;
}

.field-input2 {
  width: 100%;
  border: 1px #dee0e0 solid;
  border-radius: 10px;
  background: none;
  padding: 12px 20px;
  height: 50px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  color: #3f3f40;
}

.checklist {
  font-size: 0;
}
.checklist__item .order-checkbox-text.checked {
  border: 1px solid #1466b9;
}
.checklist__item:not(:last-child) {
  margin-bottom: 10px;
}
.checklist[data-item-expandable=collapsed] .checklist__item[data-item-expandable-item] {
  display: none;
}

.checkbox {
  position: relative;
  font-size: 0;
}
.checkbox__input {
  pointer-events: none;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}
.checkbox__visual {
  cursor: pointer;
  display: inline-flex;
  width: 16px;
  height: 16px;
  border: 1px solid #dee0e0;
  background-color: #fff;
  justify-content: center;
  align-items: center;
}
.checkbox__visual svg {
  fill: var(--theme-color-accent);
  opacity: 0;
}
.checkbox__input:checked + .checkbox__visual {
  border-color: var(--theme-color-accent);
}
.checkbox__input:checked + .checkbox__visual svg {
  opacity: 1;
}
.checkbox__input:not(:disabled):focus-visible + .checkbox__visual, .checkbox__input:not(:disabled):hover + .checkbox__visual {
  border-color: var(--theme-color-accent);
}

.radio {
  position: relative;
  font-size: 0;
}
.radio__input {
  pointer-events: none;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}
.radio__visual {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  border: 1px var(--theme-color-accent) solid;
  border-radius: 50%;
}
.radio__input:checked + .radio__visual {
  border-width: 6px;
}

.checkbox-text {
  display: inline-flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  cursor: pointer;
}
.checkbox-text.checkbox-center {
  align-items: center;
}
.checkbox-text img {
  width: 60px;
  height: 60px;
  object-fit: contain;
  margin: 0px 20px 0px 0px;
}
.checkbox-text__titles {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}
.checkbox-text__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
}
@media (max-width: 767px) {
  .checkbox-text__title {
    font-size: 14px;
    line-height: 171%;
  }
}
.checkbox-text__subtitle {
  margin: 15px 0px 0px 0px;
  font-weight: 400;
  font-size: 14px;
  color: rgba(63, 63, 64, 0.6);
}
@media (max-width: 767px) {
  .checkbox-text__subtitle {
    font-size: 12px;
    margin: 5px 0px 0px 0px;
  }
}
.checkbox-text__price {
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  margin: 0px 0px 0px 10px;
}
@media (max-width: 992px) {
  .checkbox-text__price {
    display: none;
  }
}
.checkbox-text__price-mob {
  display: none;
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 14px;
  line-height: 171%;
  margin: 5px 0px 0px 0px;
}
@media (max-width: 992px) {
  .checkbox-text__price-mob {
    display: block;
  }
}
.checkbox-text .checkbox,
.checkbox-text .radio {
  margin-right: 10px;
}
.checkbox-text__label {
  width: 100%;
  justify-content: space-between;
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
  color: var(--theme-color-text-common);
  min-height: 20px;
  display: flex;
}
.checkbox-text__label a {
  color: var(--theme-color-accent);
}

.select {
  display: inline-block;
  position: relative;
  z-index: 100;
}
.select__handle {
  cursor: pointer;
  position: relative;
  height: 44px;
  border-radius: 30px;
  border: 1px solid rgba(30, 30, 30, 0.3);
  display: inline-flex;
  padding: 5px 50px 5px 20px;
  justify-content: flex-start;
  align-items: center;
  font-size: 14px;
  line-height: 1;
  color: var(--theme-color-text-common);
  transition: border-color var(--theme-animation-duration-fast);
}
.select__handle:focus-visible {
  outline: none;
  border-color: var(--theme-color-accent);
}
.select__handle:hover {
  border-color: #1e1e1e;
}
.select__chevron {
  position: absolute;
  top: 50%;
  right: 25px;
  transform: translate(0, -35%);
}
.select.dropdown-active .select__chevron {
  transform: translate(0, -35%) rotate(180deg);
}
.select.dropdown-active {
  z-index: 101;
}
.select__option {
  padding-top: 10px;
  padding-bottom: 10px;
}
.select__dropdown .ts-dropdown-content {
  margin-top: -10px;
  margin-bottom: -10px;
}
.select__dropdown.dropdown {
  width: 100%;
  min-width: 244px;
  left: 0;
  top: calc(100% + 4px);
  font-size: 14px;
  line-height: 1.2;
  padding: 22px 20px;
}
.select__dropdown.dropdown a {
  color: var(--theme-color-text-common);
}
.select__dropdown.dropdown a:hover {
  color: var(--theme-color-accent);
}
.select__dropdown.dropdown li:not(:last-child) {
  margin-bottom: 20px;
}
.select_active .select__handle {
  border-color: var(--theme-color-accent);
}
.select_active .select__chevron {
  transform: translate(0, -35%) rotate(180deg);
}
.select_1 .select__handle {
  border-radius: 4px;
  padding-right: 40px;
}
.select_1 .select__handle:focus-visible {
  border-color: var(--theme-color-text-common);
}
.select_1 .select__chevron {
  right: 18px;
}
.select_1 .dropdown {
  box-shadow: none;
  border-radius: 4px;
  border: 1px solid rgba(30, 30, 30, 0.3);
  color: #82879a;
}
.select_wide {
  width: 100%;
}
.select_wide .select__handle {
  width: 100%;
}

.tomselected {
  display: none !important;
}

.ts-wrapper {
  position: relative;
  z-index: 1;
  display: inline-block;
  width: 100%;
}
.ts-wrapper_wide {
  display: block;
  width: 100%;
}
.ts-wrapper_wide .ts-control {
  width: 100%;
  display: flex;
}
.ts-wrapper.dropdown-active {
  z-index: 5;
}

.ts-control {
  width: 100%;
  height: 44px;
  border: 1px solid #dee0e0;
  border-radius: 6px;
  background-color: transparent;
  padding: 15px;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
  outline: none;
}
.ts-control svg {
  position: absolute;
  top: 50%;
  right: 18px;
