.spollers {
    display: grid;
    gap: 16px 0px;
    @media (max-width: 767px) {
        gap: 20px 0px;
    }
    &__item {
        background-color: rgba(247, 247, 247, 0.7);
        border-radius: 10px;
        border: 1px solid transparent;
        transition:
            background-color 0.3s ease 0s,
            border 0.3s ease 0s;
        &._spoller-active {
            background-color: #fff;
            border: 1px solid var(--theme-color-accent);
            .spollers__titles {
                &::after {
                    content: "";
                    transform: rotate(-180deg);
                    background-image: url("data:image/svg+xml,%3Csvg width='24' height='14' viewBox='0 0 24 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.81996 1.16669L12.0018 12.0556L22.1836 1.16669' stroke='%231466b9' stroke-width='2' stroke-linecap='round' stroke-linejoin='round' /%3E%3C/svg%3E");
                }
            }
        }
    }

    &__titles {
        padding: 40px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        text-align: left;
        &::after {
            content: "";
            display: block;
            width: 24px;
            height: 14px;
            background-image: url("data:image/svg+xml,%3Csvg width='24' height='14' viewBox='0 0 24 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.81996 1.16669L12.0018 12.0556L22.1836 1.16669' stroke='%233F3F40' stroke-width='2' stroke-linecap='round' stroke-linejoin='round' /%3E%3C/svg%3E");
            @media (max-width: 480px) {
                width: 23px;
                height: 13px;
                margin: 0px 0px 0px 10px;
            }
        }
        @media (max-width: 767px) {
            padding: 20px;
        }
    }

    &__left {
        flex: 1 1 auto;
        margin: 0px 20px 0px 0px;
    }

    &__title {
        font-weight: 700;
        font-size: 24px;
        line-height: 120%;
        margin: 0px 0px 12px 0px;
        color: var(--theme-color-text-common);
        @media (max-width: 767px) {
            font-size: 18px;
            margin: 0px 0px 20px 0px;
        }
    }

    &__subtitle {
        display: flex;
        flex-wrap: wrap;
        font-weight: 400;
        font-size: 16px;
        color: rgba(63, 63, 64, 0.5);
        gap: 1px;
        @media (max-width: 767px) {
            flex-direction: column;
            font-size: 14px;
        }
    }

    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        text-align: left;
        @media (max-width: 480px) {
            flex-direction: column;
            justify-content: start;
            align-items: start;
        }
    }

    &__price {
        white-space: nowrap;
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
        margin: 0px 40px 0px 0px;
        color: var(--theme-color-text-common);
        @media (max-width: 767px) {
            font-size: 18px;
            margin: 0px 20px 0px 0px;
        }
        @media (max-width: 480px) {
            margin: 20px 0px 0px 0px;
        }
    }

    &__body {
        border-top: 0.5px solid rgba(63, 63, 64, 0.3);
        margin: 0px 40px 40px 40px;
        padding: 30px 0px 0px 0px;
        @media (max-width: 767px) {
            margin: 0px 20px 20px 20px;
            padding: 20px 0px 0px 0px;
        }
        h4 {
            @media (max-width: 767px) {
                font-size: 14px;
                margin: 0px 0px 10px 0px;
            }
        }
        ul {
            margin: 0px 0px 24px 0px;
            @media (max-width: 767px) {
                margin: 0px 0px 30px 0px;
            }
            li {
                @media (max-width: 767px) {
                    margin: 0px 0px 10px 0px;
                }
            }
        }
        .btn {
            margin: 40px 0px 0px 0px;
            font-size: 18px;
            line-height: 100%;
            text-transform: uppercase;
            padding: 24px 40px;
            border-radius: 47px;
            @media (max-width: 767px) {
                font-size: 14px;
                margin: 30px 0px 0px 0px;
                padding: 20px 40px;
            }
            @media (max-width: 480px) {
                width: 100%;
                padding: 20px;
            }
        }
    }
}
