.swiper {
  &-nav {
    width: 62px;
    height: 48px;
    border-radius: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px var(--theme-color-accent) solid;

    &_prev {
      & svg {
        transform: rotate(180deg);
      }
    }

    & svg {
      fill: var(--theme-color-text-common);
    }

    &:not(:disabled):hover svg,
    &:not(:disabled):focus-visible svg {
      fill: var(--theme-color-accent);
    }

    &:disabled,
    &_disabled {
      cursor: default;
    }

    @media (max-width: 768px) {
      width: 51px;
      height: 40px;
      border-radius: 20px;

      svg {
        max-height: 14px;
      }
    }

    @media (max-width: 500px) {
      width: 40px;
      height: 32px;
      border-radius: 16px;

      svg {
        max-height: 11px;
      }
    }
  }

  &-nav1 {
    --width: 14px;
    --height: 34px;

    width: var(--width);
    height: var(--height);
    position: absolute;
    top: calc(50% - (var(--height) / 2));
    z-index: 5;

    svg {
      display: block;
      stroke: #fff;
      fill: none;
      margin: auto;
    }

    &::after {
      content: "";
      position: absolute;
      transform: translate(-50%, 0);
      left: 50%;
      top: 0;
      width: var(--height);
      height: 100%;
    }

    &_prev {
      left: 30px;
    }

    &_next {
      right: 30px;

      svg {
        transform: rotate(180deg);
      }
    }
  }
}

.swiper-navs {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  .swiper-nav_prev {
    margin-right: 10px;
  }

  @media (max-width: 500px) {
    .swiper-nav_prev {
      margin-right: 8px;
    }
  }
}

.swiper-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  & > *:first-child {
    flex-grow: 1;
    margin-right: 20px;
  }
}

.swiper + .swiper-navbar {
  margin-top: 20px;

  @media (max-width: 500px) {
    margin-top: 15px;
  }
}

.swiper-pagination.swiper-pagination_custom {
  position: relative;
  font-size: 0;
  top: unset;
  bottom: unset;
  text-align: left;

  .swiper-pagination-bullet {
    margin: 5px;
    width: 20px;
    height: 2px;
    background-color: #cdcdcd;
    border-radius: 0;
    opacity: 1;

    @include transition(width var(--theme-animation-duration));

    &.swiper-pagination-bullet-active {
      width: 100px;
      background-color: var(--theme-color-accent);
    }
  }
}

.block-news-mp .swiper-pagination.swiper-pagination_custom {
  margin-left: -2px;
  margin-right: -2px;
}

.block-news-mp .swiper-pagination.swiper-pagination_custom .swiper-pagination-bullet {
  margin: 2px;
  width: 3.5%;
  height: 2px;

  &.swiper-pagination-bullet-active {
    width: 14%;
  }
}

.block-similar .swiper-pagination.swiper-pagination_custom {
  margin-left: -2px;
  margin-right: -2px;
}

.block-similar .swiper-pagination.swiper-pagination_custom .swiper-pagination-bullet {
  margin: 2px;
  width: 3.5%;
  height: 2px;

  &.swiper-pagination-bullet-active {
    width: 14%;
  }
}

.block-similar .swiper + .swiper-navbar {
  margin-top: 20px;
}

.swiper-pagination:not(.swiper-pagination_custom) {
  --swiper-theme-color: var(--theme-color-accent);
  --swiper-pagination-bullet-inactive-color: #fff;
  --swiper-pagination-bullet-inactive-opacity: 1;
  --swiper-pagination-bullet-width: 12px;
  --swiper-pagination-bullet-height: 12px;
  --swiper-pagination-bottom: 26px;

  @media (max-width: 640px) {
    --swiper-pagination-bullet-width: 9px;
    --swiper-pagination-bullet-height: 9px;
  }
}

.swiper-news {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;

  @media (max-width: 640px) {
    width: calc(100% + var(--container-padding));

    .swiper-slide {
      width: 77.4vw;
    }
  }
}

.swiper-popular {
  border-radius: 10px;

  .swiper-slide {
    height: auto;
  }

  .card-popular {
    height: 100%;
  }
}

.swiper-intro1 {
  height: 100%;
  border-radius: 10px;
  min-height: 439px;

  .swiper-slide {
    overflow: hidden;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(36, 36, 36, 0.5);
      z-index: 1;
    }
  }

  &__pic {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    object-fit: cover;
    object-position: center;
    transform: scale(1.05);

    @include transition(transform 1500ms);
  }

  .swiper-slide-active &__pic {
    transform: scale(1);
  }

  &__content {
    position: relative;
    padding: 73px 80px 80px 80px;
    z-index: 2;
    opacity: 0;

    @include transition(opacity 500ms);
  }

  .swiper-slide-active &__content {
    opacity: 1;
  }

  &__title {
    font-size: 36px;
    font-weight: 600;
    line-height: 1.1;
    color: #fff;
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    color: rgba(255, 255, 255, 0.8);
  }

  &__title + &__text {
    margin-top: 9px;
  }

  .btn {
    margin-top: 33px;
  }
}

