.btn {
  --height: 48px;

  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.25;
  padding: 5px 24px;
  border-radius: calc(var(--height) / 2);
  min-height: var(--height);
  position: relative;

  @include transition(background-color var(--theme-animation-duration));

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  & svg + * {
    margin-left: 12px;
  }

  & span + svg {
    margin-left: 10px;
  }

  &_xsmall {
    font-size: 14px;
    min-height: 38px;
    border-radius: 5px;
  }

  &_small {
    --height: 36px;

    font-size: 14px;
    font-weight: 400;
  }

  &_big {
    --height: 60px;

    text-transform: uppercase;
    padding-left: 40px;
    padding-right: 40px;
  }

  &_wide {
    display: flex;
    width: 100%;
  }

  &_primary {
    background-color: var(--theme-color-accent);
    color: #fff;

    &.btn_hollow {
      background: none;
      border: 1.5px var(--theme-color-accent) solid;
      color: var(--theme-color-accent);

      &:hover {
        background-color: var(--theme-color-accent);
        color: #fff;
      }
    }

    svg {
      fill: #fff;
    }

    &:not(.btn_hollow):focus-visible,
    &:not(.btn_hollow):hover {
      background-color: var(--theme-color-accent-80);
    }
  }

  &_white {
    background-color: #fff;
    color: inherit;

    &.btn_hollow {
      background: none;
      border: 1px #fff solid;
      color: #fff;
    }

    &:not(.btn_hollow):hover {
      color: var(--theme-color-accent);
    }
  }

  &_black {
    background-color: var(--theme-color-text-common);
    color: #fff;

    &.btn_hollow {
      background: none;
      border: 1px var(--theme-color-text-common) solid;
      color: var(--theme-color-text-common);
    }
  }

  &_grey {
    background-color: #dee0e0;
    color: #fff;

    &.btn_hollow {
      background: none;
      border: 1px #dee0e0 solid;
      color: inherit;
    }

    &:hover:not(:disabled, .btn_hollow),
    &:focus-visible:not(:disabled, .btn_hollow) {
      background-color: var(--theme-color-accent);
    }

    &.btn_hollow:hover:not(:disabled),
    &.btn_hollow:focus-visible:not(:disabled) {
      border-color: var(--theme-color-accent);
      color: var(--theme-color-accent);
    }

    &:disabled {
      opacity: 0.5;
    }

    svg {
      fill: var(--theme-color-text-common);
    }

    &:hover svg {
      fill: var(--theme-color-accent);
    }
  }

  &_arrow1 {
    text-transform: uppercase;

    svg {
      transform: rotate(-45deg);
      margin-left: 8px;
    }

    &:hover:not(:disabled) svg,
    &:focus-visible:not(:disabled) svg {
      fill: var(--theme-color-accent);
    }
  }

  &_circle {
    width: var(--height);
    padding: 0;
  }

  &_social.btn {
    --height: 34px;

    padding: 0;

    svg {
      display: block;
    }
  }

  &:disabled {
    cursor: default;
  }

  &__count {
    position: absolute;
    top: 0;
    right: 0;
    height: 18px;
    min-width: 18px;
    border-radius: 9px;
    color: #fff;
    font-size: 11px;
    font-weight: 400;
    line-height: 1;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 0 5px;
    background-color: var(--theme-color-accent);
  }

  @media (max-width: 768px) {
    --height: 39px;

    font-size: 14px;

    &:not(.btn_circle) {
      padding: 5px 20px;
    }

    & svg + * {
      margin-left: 8px;
    }

    & span + svg {
      margin-left: 8px;
    }

    &_big {
      --height: 52px;

      font-size: 14px;
      padding-left: 35px;
      padding-right: 35px;
    }
  }

  @media (max-width: 500px) {
    --height: 32px;

    font-size: 12px;

    &:not(.btn_circle) {
      padding: 3px 15px;
    }

    &_arrow1 {
      svg {
        max-height: 15px;
        margin-left: 7px;
      }
    }

    .nomobile {
      display: none;
    }

    &_big {
      --height: 44px;

      font-size: 12px;
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}

.dropdown {
  position: relative;

  & > .btn {
    z-index: 1;
  }

  &__dropdown {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    border-radius: 18px;
    padding-top: 36px;
    border: 1px var(--theme-color-accent) solid;
    overflow: hidden;
    display: none;
  }

  &__dropdown-inner {
    background-color: #fff;
    padding: 6px 24px 14px 24px;

    & > *:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &.dropdown_active {
    z-index: 2;

    .btn {
      border-color: transparent !important;
    }
  }

  &.dropdown_active &__dropdown {
    display: block;
  }

  .socials {
    & > * {
      display: flex;
      justify-content: flex-start;
      align-items: center;

      &:not(:last-child) {
        margin-bottom: 12px;
      }
    }

    a > span:not(.btn) {
      font-size: 14px;
      font-weight: 400;
      line-height: 1;
    }

    .btn {
      --height: 20px;

      flex-shrink: 0;
      margin-right: 10px;

      svg {
        transform: scale(0.7);
      }
    }
  }
}

.btn-catalog {
  font-size: 18px;
  font-weight: 400;

  svg {
    fill: #fff;
  }

  svg + * {
    margin-left: 16px;
  }
}

.btn-burger {
  width: 32px;
  height: 18px;
  position: relative;

  svg {
    stroke: var(--theme-color-accent);
  }

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 32px;
    height: 32px;
  }
}

.btn-burger-close {
  width: 32px;
  height: 18px;
  position: relative;
  display: none;

  svg {
    stroke: var(--theme-color-accent);
  }

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 32px;
    height: 32px;
  }
}

.form-group {
  position: relative;

  &__error {
    position: absolute;
    right: 5px;
    bottom: 3px;
    font-size: 12px;
    color: #b91414;
  }

  &_error .field-input1,
  &_error .field-input2 {
    border: 1px solid #b91414 !important;
  }
}

.form-group1 {
  position: relative;
  background-color: #fff;
  border-radius: 10px;

  &__icon {
    cursor: pointer;
    position: absolute;
    right: 20px;
    top: 46px;
    svg {
      fill: #9f9fa0;
    }
    &.hidden {
      display: none;
    }
    &.open {
      top: 39px;
      right: 18px;
    }
  }

  &__placeholder {
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translate(0, -50%);
    color: #313c41;
    font-size: 14px;
    line-height: 1.25;
    pointer-events: none;

    &_req::after {
      content: "*";
      color: var(--theme-color-accent);
      margin-left: 3px;
    }
  }

  .field-input1:focus-visible + &__placeholder,
  .field-input1:not(:placeholder-shown) + &__placeholder {
    opacity: 0;
  }

  textarea.field-input1 + &__placeholder {
    top: 17px;
    transform: none;
  }
}

.form-group2 {
  position: relative;

  &__error {
    position: absolute;
    right: 5px;
    bottom: 3px;
    font-size: 12px;
    color: #b91414;
  }

  &__label {
    display: block;
    color: rgba(63, 63, 64, 0.5);
    font-size: 16px;
    line-height: 1.25;
    margin-bottom: 8px;

    &_req::after {
      content: "*";
      color: #cd1311;
      margin-left: 5px;
    }
  }

  @media (max-width: 768px) {
    &__label {
      font-size: 14px;
      margin-bottom: 8px;
    }
  }
}

.field-input1 {
  width: 100%;
  height: 52px;
  background: none;
  padding: 12px 20px;
  font-size: 14px;
  line-height: 1.25;
  border-radius: 10px;
  border: 1px transparent solid;
  color: var(--theme-color-text-common);
}

textarea.field-input1 {
  height: 108px;
  padding-top: 17px;
  resize: none;
}

.field-input2 {
  width: 100%;
  border: 1px #dee0e0 solid;
  border-radius: 10px;
  background: none;
  padding: 12px 20px;
  height: 50px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  color: #3f3f40;
}

.checklist {
  font-size: 0;

  &__item {
    .order-checkbox-text {
      &.checked {
        border: 1px solid #1466b9;
      }
    }
  }

  &__item:not(:last-child) {
    margin-bottom: 10px;
  }

  &[data-item-expandable="collapsed"] &__item[data-item-expandable-item] {
    display: none;
  }
}

.checkbox {
  position: relative;
  font-size: 0;

  &__input {
    pointer-events: none;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
  }

  &__visual {
    cursor: pointer;
    display: inline-flex;
    width: 16px;
    height: 16px;
    border: 1px solid #dee0e0;
    background-color: #fff;
    justify-content: center;
    align-items: center;

    & svg {
      fill: var(--theme-color-accent);
      opacity: 0;
    }
  }

  &__input:checked + &__visual {
    border-color: var(--theme-color-accent);

    & svg {
      opacity: 1;
    }
  }

  &__input:not(:disabled):focus-visible + &__visual,
  &__input:not(:disabled):hover + &__visual {
    border-color: var(--theme-color-accent);
  }
}

.radio {
  position: relative;
  font-size: 0;

  &__input {
    pointer-events: none;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
  }

  &__visual {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    border: 1px var(--theme-color-accent) solid;
    border-radius: 50%;
  }

  &__input:checked + &__visual {
    border-width: 6px;
  }
}

.checkbox-text {
  display: inline-flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  cursor: pointer;

  &.checkbox-center {
    align-items: center;
  }

  img {
    width: 60px;
    height: 60px;
    object-fit: contain;
    margin: 0px 20px 0px 0px;
  }

  &__titles {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
  }

  &__title {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    @media (max-width: 767px) {
      font-size: 14px;
      line-height: 171%;
    }
  }

  &__subtitle {
    margin: 15px 0px 0px 0px;
    font-weight: 400;
    font-size: 14px;
    color: rgba(63, 63, 64, 0.6);
    @media (max-width: 767px) {
      font-size: 12px;
      margin: 5px 0px 0px 0px;
    }
  }

  &__price {
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    margin: 0px 0px 0px 10px;
    @media (max-width: 992px) {
      display: none;
    }
  }

  &__price-mob {
    display: none;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 14px;
    line-height: 171%;
    margin: 5px 0px 0px 0px;
    @media (max-width: 992px) {
      display: block;
    }
  }

  .checkbox,
  .radio {
    margin-right: 10px;
  }

  &__label {
    width: 100%;
    justify-content: space-between;
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: var(--theme-color-text-common);
    min-height: 20px;
    display: flex;

    & a {
      color: var(--theme-color-accent);
    }
  }
}

.select {
  display: inline-block;
  position: relative;
  z-index: 100;

  &__handle {
    cursor: pointer;
    position: relative;
    height: 44px;
    border-radius: 30px;
    border: 1px solid rgba(30, 30, 30, 0.3);
    display: inline-flex;
    padding: 5px 50px 5px 20px;
    justify-content: flex-start;
    align-items: center;
    font-size: 14px;
    line-height: 1;
    color: var(--theme-color-text-common);

    @include transition(border-color var(--theme-animation-duration-fast));

    &:focus-visible {
      outline: none;
      border-color: var(--theme-color-accent);
    }

    &:hover {
      border-color: #1e1e1e;
    }
  }

  &__chevron {
    position: absolute;
    top: 50%;
    right: 25px;
    transform: translate(0, -35%);
  }

  &.dropdown-active &__chevron {
    transform: translate(0, -35%) rotate(180deg);
  }

  &.dropdown-active {
    z-index: 101;
  }

  &__option {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &__dropdown .ts-dropdown-content {
    margin-top: -10px;
    margin-bottom: -10px;
  }

  &__dropdown.dropdown {
    width: 100%;
    min-width: 244px;
    left: 0;
    top: calc(100% + 4px);
    font-size: 14px;
    line-height: 1.2;
    padding: 22px 20px;

    & a {
      color: var(--theme-color-text-common);

      &:hover {
        color: var(--theme-color-accent);
      }
    }

    & li:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &_active &__handle {
    border-color: var(--theme-color-accent);
  }

  &_active &__chevron {
    transform: translate(0, -35%) rotate(180deg);
  }

  &_1 &__handle {
    border-radius: 4px;
    padding-right: 40px;

    &:focus-visible {
      border-color: var(--theme-color-text-common);
    }
  }

  &_1 &__chevron {
    right: 18px;
  }

  &_1 .dropdown {
    box-shadow: none;
    border-radius: 4px;
    border: 1px solid rgba(30, 30, 30, 0.3);
    color: #82879a;
  }

  &_wide {
    width: 100%;
  }

  &_wide &__handle {
    width: 100%;
  }
}

.tomselected {
  display: none !important;
}

.ts-wrapper {
  position: relative;
  z-index: 1;
  display: inline-block;
  width: 100%;

  &_wide {
    display: block;
    width: 100%;

    & .ts-control {
      width: 100%;
      display: flex;
    }
  }

  &.dropdown-active {
    z-index: 5;
  }
}

.ts-control {
  width: 100%;
  height: 44px;
  border: 1px solid #dee0e0;
  border-radius: 6px;
  background-color: transparent;
  padding: 15px;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
  outline: none;

  & svg {
    position: absolute;
    top: 50%;
    right: 18px;
    transform: translateY(-50%);
    fill: var(--theme-color-text-common);
  }

  & .item {
    color: var(--theme-color-text-common);
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: #3f3f40;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    white-space: nowrap;
  }
}

.ts-wrapper.focus .ts-control {
  border-color: #acacac;
}

.ts-wrapper.disabled {
  background-color: #f5f5f5;
}

.ts-wrapper.disabled .ts-control {
  cursor: default;
}

.ts-wrapper.disabled .ts-control svg {
  opacity: 0.5;
}

.ts-wrapper.dropdown-active .ts-control {
  & svg {
    transform: translateY(-50%) scale(-1);
  }
}

.ts-dropdown {
  border: none;
  position: absolute;
  left: 0;
  top: calc(100% + 4px);
  border: 1px solid #dee0e0;
  border-radius: 6px;
  min-width: 100%;
  padding: 15px;
  overflow-x: visible;
  overflow-y: auto;
  max-height: 375px;
  background-color: #fff;
}

.ts-dropdown-content {
  margin-top: -10px;
  margin-bottom: -10px;
}

.ts-dropdown .option {
  font-size: 14px;
  line-height: 1.25;
  padding: 10px 0;
  cursor: pointer;
  color: var(--theme-color-text-common);

  &.selected {
    color: var(--theme-color-accent);
  }

  &:not([aria-disabled="true"]):hover {
    color: var(--theme-color-accent);
  }

  &[aria-disabled="true"] {
    opacity: 0.5;
    cursor: default;
  }
}

.input-group1 {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  gap: 0 8px;

  &__field {
    flex-shrink: 1;
    flex-grow: 1;

    &::placeholder {
      color: #82879a;

      @include transition(color var(--theme-animation-duration-fast));
    }

    &:focus::placeholder {
      color: transparent;
    }
  }

  &__btn {
    width: 156px;
    font-size: 14px;
    flex-shrink: 0;
    flex-grow: 0;
  }

  &__btn-arr {
    width: 60px;
    flex-shrink: 0;
    flex-grow: 0;

    & svg {
      width: 16px;
      height: 14px;
      fill: #fff;
    }
  }
}

.pagination {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 12px 12px;
  margin: 50px 0px 0px 0px;
  @media (max-width: 767px) {
    margin: 30px 0px 0px 0px;
    gap: 0px 0px;
  }

  &__item {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    font-size: 14px;
    color: var(--theme-color-text-common);
    outline: none;

    @include transition(
      background-color var(--theme-animation-duration-fast) 100ms,
      color var(--theme-animation-duration-fast)
    );
    @media (max-width: 767px) {
      height: 32px;
      flex: 0 0 32px;
      width: 32px;
      font-size: 12px;
    }

    &.pagination__item_nav-prev {
      margin: 0px 14px 0px 0px;
      @media (max-width: 767px) {
        margin: 0px 10px 0px 0px;
      }
    }

    &.pagination__item_nav-next {
      margin: 0px 0px 0px 14px;
      @media (max-width: 767px) {
        margin: 0px 0px 0px 10px;
      }
    }

    &:not(.pagination__item_active, .pagination__item_disabled):hover,
    &:not(.pagination__item_active, .pagination__item_disabled):focus-visible {
      color: var(--theme-color-accent);
      background-color: #f4f7fb;

      & svg {
        fill: var(--theme-color-accent);
      }
    }

    &_active {
      background-color: var(--theme-color-accent);
      color: #fff;
    }

    &_nav {
      background-color: #f7f7f7;
      border-radius: 24px;
      width: 52px;
      height: 40px;
      @media (max-width: 767px) {
        width: 40px;
        height: 32px;
        flex: 0 0 40px;
      }

      & svg {
        fill: #3f3f40;
        @media (max-width: 767px) {
          width: 14px;
          height: 16px;
        }
      }

      &-prev {
        transform: rotate(180deg);
      }
    }

    &_ellipsis {
      pointer-events: none;
      flex: 0 0 20px;
      width: 20px;
    }

    &_disabled {
      & svg {
        fill: #82879a;
      }
    }
  }
}

.expandable {
  & [data-expandable-handle] {
    cursor: pointer;

    &:hover {
      & .expandable__label {
        color: var(--theme-color-accent);
      }

      & .expandable__chevron {
        fill: var(--theme-color-accent);
      }
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 0 20px;
    margin: 0;
    transition: 250ms margin ease;
  }

  &__label {
    font-size: 16px;
    font-weight: 700;
    color: var(--theme-color-text-common);

    @include transition(color var(--theme-animation-duration-fast));
  }

  &__chevron {
    display: block;
    width: 8px;
    height: 13px;
    transform: rotate(90deg);
    position: relative;
    top: 1px;
    right: 5.27px;

    @include transition(fill var(--theme-animation-duration-fast));
  }

  &__bottom {
    display: grid;
    grid-template-rows: 0fr;
    opacity: 0;

    @include transition(grid-template-rows var(--theme-animation-duration), opacity var(--theme-animation-duration));

    &-inner {
      overflow: hidden;
    }
  }

  &[data-expandable="expanded"] &__bottom {
    grid-template-rows: 1fr;
    opacity: 1;
  }

  &[data-expandable="expanded"] &__chevron {
    transform: rotate(270deg);
  }

  &[data-expandable="expanded"] &__top {
    margin: 0 0 25px 0;
  }
}

.stepcounter {
  position: relative;
  flex: 0 0 136px;
  width: 136px;
  height: 48px;
  display: inline-flex;
  justify-content: flex-start;
  align-items: stretch;
  @media (max-width: 767px) {
    flex: 0 0 102px;
    width: 102px;
    height: 38px;
  }

  &__btn {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #dee0e0;
    flex: 0 0 48px;
    height: 48px;
    border-radius: 50%;
    background-color: #fff;
    transition: border 0.3s ease;
    @media (max-width: 767px) {
      flex: 0 0 38px;
      height: 38px;
    }

    & svg {
      fill: var(--theme-color-text-common);
    }

    &:hover {
      border: 1px solid var(--theme-color-accent);
    }

    &[data-stepcounter="-"] {
      svg {
        width: 16px;
        height: 2px;
        @media (max-width: 767px) {
          width: 12px;
        }
      }
    }

    &[data-stepcounter="+"] {
      svg {
        width: 16px;
        height: 16px;
        @media (max-width: 767px) {
          width: 12px;
          height: 12px;
        }
      }
    }

    &:disabled {
      cursor: default;

      & svg {
        fill: #82879a;
      }
    }

    &:focus-visible {
      position: relative;
      z-index: 1;
      outline: 1px var(--theme-color-accent) solid;
    }
  }

  &__input {
    width: 40px;
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;
    text-align: center;
    color: var(--theme-color-text-common);
    @media (max-width: 767px) {
      width: 26px;
    }

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      appearance: none;
      margin: 0;
    }
  }

  &_accent {
    border: none;
    height: 48px;
    background-color: transparent;
    @media (max-width: 767px) {
      height: 38px;
    }
  }

  &_accent &__input {
    width: 40px;
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;
    text-align: center;
    color: var(--theme-color-text-common);
    flex-grow: 1;
    @media (max-width: 767px) {
      width: 26px;
    }
  }

  &_accent &__btn {
    flex-grow: 0;

    &:disabled {
      pointer-events: none;
      opacity: 0.5;
    }
  }
}

.form-feedback {
  .btn_big {
    --height: 62px;

    margin-top: 16px;
    margin-bottom: 6px;
    font-size: 18px;
  }

  @media (max-width: 768px) {
    .btn_big {
      --height: 52px;

      font-size: 16px;
    }

    .textblock1.textblock1_small {
      font-size: 12px;
    }
  }

  @media (max-width: 500px) {
    .textblock1.textblock1_small {
      font-size: 10px;
    }

    .btn_big {
      --height: 50px;

      font-size: 14px;
    }
  }
}

.other-form {
  .form-grid1 {
    gap: 20px;
    @media (max-width: 767px) {
      gap: 12px 0px;
    }
  }
  .form-grid1__row {
    display: grid;
    &.form-grid1-row2 {
      @media (min-width: 767px) {
        grid-template-columns: 1fr 1fr;
        gap: 20px;
      }
      @media (max-width: 767px) {
        gap: 12px;
      }
    }
  }
  textarea.field-input1 {
    height: 80px;
  }
  .form-group1__placeholder {
    position: relative;
    top: 0;
    left: 0;
    transform: translate(0);
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: rgba(63, 63, 64, 0.5);
    display: block;
    margin: 0px 0px 8px 0px;
    @media (max-width: 767px) {
      font-size: 14px;
    }
  }
  .form-group1__placeholder_req {
    &::after {
      color: #cd1311;
    }
  }
  .field-input1 {
    border: 1px solid #dee0e0;
    border-radius: 10px;
    height: 50px;
    &::placeholder {
      color: rgb(63, 63, 64, 0.5);
    }
  }
  .form-group__error {
    position: relative;
    bottom: 0;
    right: 0px;
    margin: 2px 0px 0px 0px;
  }
}

.lk-form {
  .field-input1 {
    font-size: 16px;
    @media (max-width: 768px) {
      font-size: 14px;
    }
  }
}

.form {
  display: flex;
  flex-direction: column;
  gap: 40px 0px;
  @media (max-width: 768px) {
    gap: 20px;
  }
  &__column {
    p {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      margin: 0px 0px 30px 0px;
      @media (max-width: 768px) {
        font-size: 14px;
        margin: 0px 0px 20px 0px;
      }
    }
  }

  &__top {
    margin: 0px 0px 20px 0px;
  }

  &__title {
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    @media (max-width: 768px) {
      font-size: 14px;
    }
  }

  &__subtitle {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    margin: 20px 0px 0px 0px;
    @media (max-width: 768px) {
      font-size: 14px;
      margin: 10px 0px 0px 0px;
    }
  }

  &__buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    &.form-authorization {
      .btn {
        width: 275px;
      }
    }
    .textblock1 {
      text-align: center;
      margin: 0px 0px 20px 0px;
    }
    .btn {
      width: 491px;
      --height: 48px;
      text-transform: none;
      @media (max-width: 768px) {
        width: 100%;
      }
    }
  }

  &__link {
    display: flex;
    align-items: center;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
    text-transform: uppercase;
    text-align: center;
    color: var(--theme-color-accent);
    transition: color 0.3s;
    margin: 30px 0px 0px 0px;
    svg {
      fill: currentColor;
      margin: 0px 0px 0px 5px;
    }
    &:hover {
      color: var(--theme-color-accent-80);
    }
    @media (max-width: 768px) {
      margin: 20px 0px 0px 0px;
    }
  }
}

.forgot-password {
  display: block;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: var(--theme-color-accent);
  margin: 10px 0px 0px 0px;
  transition: color 0.3s;
  &:hover {
    color: var(--theme-color-accent-80);
  }
}

.form-warning {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  margin: 20px 0px 0px 0px;
  @media (max-width: 768px) {
    font-size: 14px;
  }
}
