.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(30, 30, 30, 0.4);
  backdrop-filter: blur(3px);
  z-index: 99;
  opacity: 0;
  pointer-events: none;

  @include transition(opacity var(--theme-animation-duration));

  &_active {
    pointer-events: all;
    opacity: 1;
  }
}

.hystmodal {
  padding: var(--container-padding);

  &__window {
    border-radius: 10px;
    padding: 40px 50px;
    width: 100%;
    max-width: 580px;
  }

  &__close {
    background: none;
    width: 20px;
    height: 20px;
    outline: none;
    top: 24px;
    right: 30px;
    z-index: 2;

    svg {
      fill: rgba(130, 135, 154, 0.5);
    }

    &:focus-visible svg,
    &:hover svg {
      fill: rgba(130, 135, 154, 1);
    }

    &:focus {
      outline: none;
    }
  }

  &__title {
    font-size: 30px;
    line-height: 1.2;
    font-weight: 700;
    margin-bottom: 10px;
  }

  &__description {
    margin: 0 0 31px 0;
  }

  .textblock a {
    color: var(--theme-color-accent);
    text-decoration: none;

    &:hover {
      color: var(--theme-color-accent-light);
    }
  }

  .form-grid1 {
    gap: 20px;

    .btn_big {
      --height: 62px;

      margin-top: 10px;
      margin-bottom: -4px;
    }

    .textblock1 {
      font-size: 14px;

      a {
        text-decoration: none;
      }
    }
  }

  @media (max-width: 700px) {
    &__window {
      padding: 20px;
    }

    &__title {
      font-size: 24px;
      margin-bottom: 10px;
    }

    &__description {
      line-height: 1.7;
      font-size: 14px;
      margin-bottom: 22px;
    }

    .form-grid1 {
      gap: 13px;

      .btn_big {
        --height: 50px;

        margin-top: 8px;
        margin-bottom: 2px;
      }

      .textblock1 {
        font-size: 10px;
      }
    }

    &__close {
      top: 12px;
      right: 12px;

      svg {
        width: 13px;
      }
    }
  }
}
