body {
  color: var(--theme-color-text-common);
  font-family: Inter, sans-serif;
  font-weight: 400;
  font-size: 16px;
}

body.noscroll {
  overflow: hidden;
}

input,
button,
textarea {
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
}

fieldset,
fieldset legend {
  margin: 0;
  border: none;
  padding: 0;
}

button {
  cursor: pointer;
}

a {
  outline: none;
  color: currentcolor;
}

.text-center {
  text-align: center;
}

.preload * {
  transition: none !important;
}

.v-h {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.h2-cols {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 48px;

  & .title2 {
    flex-grow: 1;
    margin-right: 20px;
    min-height: 60px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  @media (max-width: 767px) {
    & .title2 {
      min-height: 50px;
    }
  }
}

.title1 {
  font-size: 40px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;

  &::first-letter {
    color: var(--theme-color-accent);
  }

  @media (max-width: 1100px) {
    font-size: 36px;
  }

  @media (max-width: 900px) {
    font-size: 32px;
  }

  @media (max-width: 767px) {
    font-size: 26px;
  }

  @media (max-width: 640px) {
    font-size: 25px;
  }
}

.title2 {
  font-size: 32px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;
  margin-bottom: 48px;

  &::first-letter {
    color: var(--theme-color-accent);
  }

  @media (max-width: 1100px) {
    font-size: 30px;
  }

  @media (max-width: 900px) {
    font-size: 28px;
  }

  @media (max-width: 767px) {
    font-size: 26px;
  }

  @media (max-width: 640px) {
    font-size: 25px;
  }
}

.heading-complex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  flex-wrap: wrap;

  &__right {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    & > *:last-child {
      margin-left: 30px;
    }
  }

  .title2 {
    margin-bottom: 0;
  }

  @media (max-width: 767px) {
    margin-bottom: 28px;
  }

  @media (max-width: 500px) {
    margin-bottom: 17px;
  }
}

.heading-letters {
  .title1 {
    &::first-letter {
      color: var(--theme-color-text-common);
    }
  }
  .title2 {
    &::first-letter {
      color: var(--theme-color-text-common);
    }
  }
}

.spinner {
  display: inline-block;

  & svg {
    width: 26px;
    height: 22px;
    fill: var(--theme-color-text-common);
  }

  &.spinner_active svg {
    transform-origin: 15px 11px;
    animation: spinner-rotation 750ms infinite linear;
  }

  @keyframes spinner-rotation {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(-360deg);
    }
  }
}

.btn-load {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0 10px;
  font-weight: 700;
  font-size: 14px;
  line-height: 100%;
  color: inherit;
  text-transform: uppercase;
  border-radius: 4px;
  transition-timing-function: ease;
  transition-property: color;
  transition-duration: 100ms;

  svg {
    fill: currentcolor;
  }

  &:hover {
    color: var(--theme-color-accent);
  }
  @media (max-width: 767px) {
    font-size: 12px;
  }
}

.nav-bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 0px 0px 0px;
  @media (max-width: 767px) {
    margin: 30px 0px 0px 0px;
  }
}

.textblock1 {
  font-size: 16px;
  line-height: 1.5;

  & h2,
  h3,
  h4,
  h5,
  h6 {
    text-transform: uppercase;
    font-size: inherit;
    margin-bottom: 1.5em;
  }

  & p,
  ul,
  ol,
  img {
    margin-bottom: 1.5em;
  }

  & a {
    color: var(--theme-color-accent);
    text-decoration: underline;
  }

  & img {
    max-width: 100%;
  }

  & p > span {
    color: var(--theme-color-accent);
  }

  & > *:last-child {
    margin-bottom: 0;
  }

  &_small {
    font-size: 14px;
  }

  @media (max-width: 575px) {
    font-size: 14px;

    & h2,
    h3,
    h4,
    h5,
    h6 {
      margin-bottom: 1.25em;
    }

    & p,
    ul,
    ol,
    img {
      margin-bottom: 1.25em;
    }
  }
}

.video {
  position: relative;

  &__play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  @media (max-width: 767px) {
    &__play {
      width: 54px;
      height: 54px;
    }
  }
}

.main-wrapper-outer::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 49;
  pointer-events: none;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.6);

  @include transition(opacity var(--theme-animation-duration));
}

.main-wrapper-outer_faded::after {
  pointer-events: all;
  opacity: 1;
}

.phone-item {
  display: block;
  text-decoration: none;

  &__label1 {
    display: block;
    font-size: 12px;
    line-height: 1.2;
    color: rgba(63, 63, 64, 0.5);
    margin-bottom: 2px;
  }

  &__label2 {
    display: inline-block;
    font-size: 14px;
    line-height: 1.5;
    color: #3f3f40;
  }
}

.notifications-stack {
  position: fixed;
  top: 10px;
  right: var(--calculated-padding);
  z-index: 55;

  & > * {
    margin-bottom: 10px;
  }
}

.card-document {
  display: flex;
  display: flex;
  align-items: center;
  background-color: #f7f7f7;
  border-radius: 10px;
  padding: 24px;
  transition: background-color 0.3s ease 0s;
  &:hover {
    background-color: var(--theme-border-color);
  }
  @media (max-width: 480px) {
    padding: 24px 20px 24px 15px;
    width: 100%;
  }

  svg {
    fill: var(--theme-color-accent);
  }

  &__desc {
    flex: 1 1 auto;
    margin: 0px 30px 0px 24px;
    @media (max-width: 767px) {
      margin: 0px 16px 0px 15px;
    }
    span {
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: rgba(63, 63, 64, 0.5);
      @media (max-width: 480px) {
        font-size: 12px;
      }
    }
  }

  &__text {
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    margin: 0px 0px 8px 0px;
    @media (max-width: 480px) {
      font-size: 14px;
      margin: 0px 0px 11px 0px;
    }
  }
}

.back {
  margin: 0px 0px 30px 0px;
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 16px;
  line-height: 114%;
  text-transform: uppercase;
  color: var(--theme-color-accent);
  transition: opacity 0.3s ease 0s;
  & svg {
    fill: var(--theme-color-accent);
    margin: 0px 8px 0px 0px;
    width: 18px;
    height: 17px;
    transform: rotate(-180deg);
    @media (max-width: 767px) {
      width: 15px;
      height: 15px;
    }
  }
  &:hover {
    opacity: 0.8;
  }
  @media (max-width: 767px) {
    font-size: 14px;
    margin: 0px 0px 20px 0px;
  }
}

.descr {
  & > *:last-child {
    margin: 0px 0px 0px 0px;
  }
  h2 {
    margin: 0px 0px 16px 0px;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    @media (max-width: 767px) {
      font-size: 20px;
      margin: 0px 0px 20px 0px;
    }
  }
  h3 {
    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
    margin: 0px 0px 16px 0px;
    @media (max-width: 767px) {
      font-size: 18px;
      margin: 0px 0px 20px 0px;
    }
  }
  h4 {
    text-wrap: auto;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    margin: 0px 0px 16px 0px;
  }
  p {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    margin: 0px 0px 30px 0px;
    &.italic {
      font-style: italic;
      margin: 0px 0px 24px 0px;
    }
    &.hl {
      color: var(--theme-color-accent);
    }
    &.strong {
      font-weight: 600;
      margin: 0px 0px 20px 0px;
    }
    @media (max-width: 767px) {
      font-size: 14px;
      margin: 0px 0px 20px 0px;
    }
  }
  blockquote {
    position: relative;
    padding-left: 16px;
    margin-left: 0;
    margin: 0px 0px 30px 0px;
    @media (max-width: 767px) {
      margin: 0px 0px 20px 0px;
    }
    p {
      font-size: 18px;
      &:last-child {
        margin: 0px 0px 0px 0px;
      }
      @media (max-width: 767px) {
        font-size: 16px;
        margin: 0px 0px 20px 0px;
      }
    }
    &::before {
      content: "";
      border-radius: 10px;
      background-color: var(--theme-color-accent);
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 3px;
    }
  }
  ul {
    margin: 0px 0px 30px 0px;
    @media (max-width: 767px) {
      margin: 0px 0px 20px 0px;
    }
    li {
      display: flex;
      margin: 0px 0px 16px 0px;
      &:last-child {
        margin: 0px 0px 0px 0px;
      }
      &.descr-circle {
        &::before {
          content: "";
          width: 8px;
          height: 8px;
          flex: 0 0 8px;
          border-radius: 50%;
          background-color: var(--theme-color-accent);
          margin: 9px 11px 0px 0px;
          @media (max-width: 767px) {
            margin: 6px 11px 0px 0px;
          }
        }
      }
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      @media (max-width: 767px) {
        font-size: 14px;
      }
    }
  }
}

.btn-iconed {
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  text-align: center;
  color: var(--theme-color-accent);
  transition: opacity 0.3s ease;
  & svg {
    fill: currentColor;
    margin: 0px 12px 0px 0px;
  }
  &:hover {
    opacity: 0.8;
  }
  @media (max-width: 767px) {
    display: none;
  }
}

.stock {
  font-weight: 400;
  font-size: 16px;
  line-height: 81%;
  color: #27ae60;
  &.yellow {
    color: #cdae00;
  }
  @media (max-width: 767px) {
    font-size: 12px;
    line-height: 108%;
  }
}

.menu-block {
  position: relative;
  flex: 0 1 345px;
  border: 1px solid #dee0e0;
  border-radius: 10px;
  @media (min-width: 992px) {
    &:hover {
      border-radius: 10px 0px 0px 10px;
    }
  }
  @media (max-width: 992px) {
    flex: 1 1 auto;
    width: 100%;
    border: none;
    &.active {
      .menu-block__title {
        svg {
          transform: rotate(90deg);
        }
      }
      .menu-block__body {
        display: block;
      }
    }
  }

  &.hover {
    @media (min-width: 992px) {
      .menu-block__items {
        display: none;
      }
      .menu-block__subitems {
        display: none;
      }
      .menu-block__item {
        &:hover {
          .menu-block__link {
            color: var(--theme-color-accent);
          }
        }
        &:hover > .menu-block__items {
          display: block;
        }
      }
      .menu-block__subitem {
        &:hover > .menu-block__subitems {
          display: block;
        }
        &:hover {
          .menu-block__sublink {
            color: var(--theme-color-accent);
          }
        }
      }
    }
  }

  &__title {
    border: 1px solid #dee0e0;
    border-radius: 10px;
    padding: 15px;
    display: none;
    justify-content: space-between;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    & svg {
      fill: currentColor;
    }
    @media (max-width: 992px) {
      display: flex;
    }
    @media (max-width: 767px) {
      font-size: 14px;
    }
  }

  &__close {
    cursor: pointer;
    @media (min-width: 992px) {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: #f7f7f7;
    }
    @media (max-width: 992px) {
      svg {
        width: 20px;
        height: 20px;
        fill: rgba(130, 135, 154, 0.5);
      }
    }
  }

  &__body {
    padding: 0px 0px 0px 0px;
    @media (max-width: 992px) {
      display: none;
      position: absolute;
      top: 102%;
      left: 0;
      width: 100%;
      height: auto;
      background-color: #fff;
      border: 1px solid #dee0e0;
      border-radius: 10px;
      padding: 20px 15px;
      z-index: 20;
    }

    a {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-weight: 500;
      font-size: 16px;
      line-height: 150%;
      transition: color 0.3s ease;
      & svg {
        flex: 0 0 9px;
        fill: currentColor;
        margin: 0px 0px 0px 30px;
      }
      &:hover {
        color: var(--theme-color-accent);
      }
      &._spoller-active {
        color: var(--theme-color-accent);
        svg {
          transform: rotate(90deg);
        }
      }
      @media (max-width: 767px) {
        font-size: 14px;
      }
    }
  }

  &__item {
    @media (min-width: 992px) {
      position: relative;
      &:first-child {
        .menu-block__link {
          padding: 30px 30px 8px 30px;
        }
      }
      &:last-child {
        .menu-block__link {
          padding: 8px 30px 30px 30px;
        }
      }
    }
    @media (max-width: 992px) {
      padding: 0px 0px 16px 0px;
      &:last-child {
        padding: 0px 0px 0px 0px;
      }
    }
  }

  &__link {
    padding: 8px 30px;
    @media (max-width: 992px) {
      padding: 0px 0px 0px 0px;
    }
  }

  &__items {
    ul {
      display: grid;
      gap: 16px 0px;
    }
    @media (min-width: 992px) {
      position: absolute;
      left: 100%;
      height: auto;
      top: 0;
      width: 345px;
      padding: 30px 0px;
      background: #fff;
      border: 1px solid #dee0e0;
      z-index: 40;
    }
    @media (max-width: 992px) {
      padding: 15px 0px 14px 15px;
    }
  }

  &__subitem {
  }

  &__sublink {
    padding: 0px 30px;
    @media (max-width: 992px) {
      padding: 0px 0px 0px 0px;
    }
  }

  &__subitems {
    @media (min-width: 992px) {
      position: absolute;
      left: 100%;
      height: auto;
      top: 0;
      width: 345px;
      padding: 30px;
      background: #fff;
      border: 1px solid #dee0e0;
      z-index: 40;
    }
    @media (max-width: 992px) {
      padding: 15px 0px 4px 0px;
    }
  }
}
