.footer {
  width: 100%;
  background-color: #242424;
  padding-top: 80px;
  padding-bottom: 30px;

  @media (max-width: 1000px) {
    padding-top: 60px;
  }

  @media (max-width: 768px) {
    padding-top: 40px;
    padding-bottom: 20px;
  }

  @media (max-width: 500px) {
    padding-top: 20px;
  }
}

.footer-top {
  display: grid;
  grid-template-columns: max-content auto max-content;
  color: #fff;
  padding-bottom: 50px;
  gap: 20px;

  &__phone {
    font-size: 24px;
    font-weight: 600;
    line-height: 1.5;
    margin-bottom: 12px;
  }

  &__email {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    margin-bottom: 22px;

    a {
      text-decoration: underline;
    }
  }

  .socials {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: -6px;
    margin-right: -6px;

    & > * {
      margin: 0 6px;
    }

    span:not(.btn) {
      position: absolute;
      left: -10000px;
      top: auto;
      width: 1px;
      height: 1px;
      overflow: hidden;
    }
  }

  @media (max-width: 1000px) {
    padding-bottom: 40px;
  }

  @media (max-width: 900px) {
    padding-bottom: 30px;

    &__col1 a {
      display: inline-block;
      width: 150px;

      img {
        width: 100%;
        display: block;
      }
    }
  }

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 23px;
    padding-bottom: 20px;

    &__col1 a {
      width: 130px;
    }

    .socials {
      justify-content: center;
    }

    &__phone {
      margin-top: 7px;
      font-size: 22px;
      margin-bottom: 16px;
    }
  }

  @media (max-width: 640px) {
    &__phone {
      font-size: 20px;
    }
  }

  @media (max-width: 500px) {
    &__col1 a {
      width: 111px;
    }

    &__phone {
      font-size: 18px;
    }
  }
}

.footer-bottom {
  display: grid;
  grid-template-columns: 0.83fr 0.97fr 1.17fr 0.4fr;
  gap: 20px;
  color: rgba(255, 255, 255, 0.5);
  border-top: 1px rgba(255, 255, 255, 0.3) solid;
  padding-top: 40px;
  font-size: 14px;
  line-height: 1.5;

  a {
    text-decoration: underline;
    text-underline-offset: 3px;

    &:hover {
      color: #fff;
    }
  }

  p:not(:last-child) {
    margin-bottom: 0.6em;
  }

  &__col1 {
    p:not(:last-child) {
      margin-bottom: 1.35em;
    }
  }

  &__col4 {
    display: flex;
    justify-content: flex-end;
  }

  @media (max-width: 1000px) {
    padding-top: 30px;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px 20px;
    font-size: 12px;

    &__col4 {
      grid-column: span 3;
      display: flex;
      justify-content: center;
    }
  }

  @media (max-width: 768px) {
    text-align: center;
    padding-top: 20px;
    grid-template-columns: 1fr;
    gap: 15px 20px;

    &__col4 {
      grid-column: auto;

      a {
        display: inline-block;
        width: 90px;

        img {
          display: block;
          width: 100%;
        }
      }
    }
  }

  @media (max-width: 768px) {
    font-size: 10px;
  }
}

.menu3 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: -40px;
  margin-right: -40px;

  & > * {
    margin-left: 40px;
    margin-right: 40px;
  }

  a {
    text-transform: uppercase;
    display: inline-block;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.5;
    color: #fff;

    &:focus-visible::first-letter,
    &:hover::first-letter {
      color: var(--theme-color-accent);
    }
  }

  @media (max-width: 1280px) {
    margin-left: -20px;
    margin-right: -20px;

    & > * {
      margin-left: 20px;
      margin-right: 20px;
    }
  }

  @media (max-width: 1100px) {
    margin-left: 0;
    margin-right: 0;
    flex-wrap: wrap;
    margin-bottom: -20px;

    & > * {
      width: 50%;
      text-align: center;
      margin-left: 0;
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  @media (max-width: 768px) {
    a {
      font-size: 16px;
    }
  }

  @media (max-width: 500px) {
    a {
      font-size: 14px;
    }
  }

  @media (max-width: 400px) {
    margin-bottom: -18px;

    & > * {
      margin-bottom: 18px;
    }

    a {
      font-size: 12px;
    }
  }
}
